import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'
import { getUserId } from 'Shared/selectors/user'
import * as R from 'ramda'
import { formatDate } from 'Shared/helpers/date'
import { formats } from 'Shared/constants/Date'

export const getCertificatesById = state => state.certificates.entries

export const getUserCertificates = createSelector(
  getCertificatesById,
  getUserId,
  (certificate, id) => {
    return R.pipe(
      transformToArray,
      R.filter(
        R.propEq('candidateId', id)
      ),
      R.sortBy(
        R.prop('issuedAt')
      ),
      R.reverse
    )(certificate)
  }
)

export const getCertificateDetails = createSelector(
  getCertificatesById,
  (state, id) => id,
  (certificates, id) => R.propOr({}, id)(certificates)
)

export const getCertificateName = createSelector(
  getCertificatesById,
  (state, id) => id,
  (certificates, id) => R.pathOr('', [id, 'name'])(certificates)
)

export const getCertificateIssuer = createSelector(
  getCertificatesById,
  (state, id) => id,
  (certificates, id) => R.pathOr('', [id, 'issuer'])(certificates)
)

const formatByYear = date => formatDate(date, formats.year)

export const getCertificateDate = createSelector(
  getCertificatesById,
  (state, id) => id,
  (certificates, id) => R.pipe(
    R.pathOr('', [id, 'issuedAt']),
    formatByYear
  )(certificates)
)

export const hasOneCertificate = createSelector(
  getCertificatesById,
  getUserId,
  (universities, userId) => R.pipe(
    transformToArray,
    R.filter(
      R.propEq('candidateId', userId)
    ),
    R.length,
    R.equals(1)
  )(universities)
)

export const getCandidateCertificates = createSelector(
  getCertificatesById,
  (state, id) => id,
  (certificate, id) => {
    return R.pipe(
      transformToArray,
      R.filter(
        R.propEq('candidateId', id)
      ),
      R.sortBy(
        R.prop('issuedAt')
      ),
      R.reverse
    )(certificate)
  }
)

export const hasCandidateAnyCertificates = createSelector(
  getCertificatesById,
  (state, id) => id,
  (certificate, id) => {
    return R.pipe(
      transformToArray,
      R.filter(
        R.propEq('candidateId', id)
      ),
      R.isEmpty,
      R.not
    )(certificate)
  }
)



