import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import { lengthLimit } from 'Shared/constants/Editor'
import { Field, Form } from 'redux-form'
import { Textarea } from 'Shared/components/FormElements'

const [, max] = lengthLimit.requestChangeTimeSlot

class RequestTimeSlotChangeForm extends React.PureComponent {
  render () {
    const { t, handleSubmit } = this.props

    return <Form onSubmit={handleSubmit}>
      <Field
        component={Textarea}
        label={t('forms.message.label')}
        name='message'
        limit={max}
        placeholder={t('forms.message.placeholder')}
      />
    </Form>
  }
}

export default withTranslations(RequestTimeSlotChangeForm)



