import startCase from 'lodash/startCase'
import { getEditorContentLength } from 'Shared/helpers/editor'
import { lengthLimit } from 'Shared/constants/Editor'

export default values => {
  const errors = {}

  const requiredFields = ['organisationName', 'position', 'startedAt', 'type', 'description']

  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = {
        key: 'errors.requiredField',
        options: {
          field: startCase(field)
        }
      }
    }
  })

  if (!values['finishedAt'] && !values['currentWork']) {
    errors['finishedAt'] = {
      key: 'errors.requiredField',
      options: {
        field: startCase('finishedAt')
      }
    }
  }

  if (values['description'] && getEditorContentLength(values['description']) < 30) {
    errors['description'] = {
      key: 'errors.descriptionTooShort',
      options: {
        field: startCase('description'),
        chars: startCase('30')
      }
    }
  }

  if (values['description'] && getEditorContentLength(values['description']) > lengthLimit.description) {
    errors['description'] = {
      key: 'errors.charactersLength',
      options: {
        limit: lengthLimit.description
      }
    }
  }

  return errors
}



