import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'
import { getJobOfferApplicationsById } from 'Shared/selectors/jobOfferApplications'
import { any, propOr, filter, sortBy, find } from 'ramda'
import { REQUEST_CHANGE_TIME_SLOT } from 'Shared/constants/JobOfferApplicationTimeSlots'

export const getTimeSlotById = state => state.jobOfferApplicationTimeSlots.entries

const getApplicationId = (state, applicationId) => applicationId

export const hasApplicationAnyTimeSlots =
  createSelector(
    getTimeSlotById,
    getApplicationId,
    (timeSlots, applicationId) => {
      const allSlots = transformToArray(timeSlots)
      return any(slot => propOr(null, 'applicationId', slot) === applicationId, allSlots)
    }
  )

export const getApplicationTimeSlots =
  createSelector(
    getTimeSlotById,
    getApplicationId,
    (timeSlots, applicationId) => sortBy(slot => slot.dateFrom, filter(slot => propOr(null, 'applicationId', slot) === applicationId, transformToArray(timeSlots)))
  )

export const isAnyTimeSlotChosen =
  createSelector(
    getTimeSlotById,
    getApplicationId,
    getApplicationTimeSlots,
    getJobOfferApplicationsById,
    (timeSlots, applicationId, applicationTimeSlots, applications) =>
      any(slot => slot.isChosen, applicationTimeSlots) || propOr(false, 'isTimeSlotChangeRequested', applications[applicationId])
  )

export const chosenTimeSlotId =
  createSelector(
    getTimeSlotById,
    getApplicationId,
    getApplicationTimeSlots,
    getJobOfferApplicationsById,
    (timeSlots, applicationId, applicationTimeSlots, applications) => {
      const selectedTimeSlotId = propOr(false, 'id', find(slot => slot.isChosen, applicationTimeSlots))
      const selectedRequestChangeName = propOr(false, 'isTimeSlotChangeRequested', applications[applicationId]) && REQUEST_CHANGE_TIME_SLOT

      return selectedTimeSlotId || selectedRequestChangeName
    }
  )

export const chosenTimeSlot =
  createSelector(
    getTimeSlotById,
    getApplicationId,
    getApplicationTimeSlots,
    (timeSlots, applicationId, applicationTimeSlots) => find(slot => slot.isChosen, applicationTimeSlots)
  )



