import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { PreviewModalMobile } from 'Shared/components/Modal'
import withTranslations from 'Shared/hoc/withTranslations'
import Button from 'Shared/components/Button'
import './DeleteModal.scss'

class DeleteModal extends Component {
  static propTypes = {
    handleDelete: PropTypes.func.isRequired,
    isModalVisible: PropTypes.bool.isRequired,
    hideDeleteModal: PropTypes.func.isRequired
  }

  render () {
    const { t, hideDeleteModal, isModalVisible, handleDelete, title, content, cancelText, submitText } = this.props

    return (
      <PreviewModalMobile
        title={title}
        handleClose={hideDeleteModal}
        show={isModalVisible}
      >
        <p className='delete-modal-mobile__content'>{content}</p>
        <div className='delete-modal-mobile__buttons'>
          <Button onClick={hideDeleteModal}>{cancelText || t('common.cancel')}</Button>
          <Button className='delete-modal-mobile__submit' onClick={handleDelete}>{submitText || t('common.delete')}</Button>
        </div>
      </PreviewModalMobile>
    )
  }
}

export default withTranslations(DeleteModal)



