import React, { Component, Fragment } from 'react'
import ReactModal from 'react-modal'
import ReactPlayer from 'Shared/components/AsyncReactPlayer'
import cx from 'classnames'
import { isArabicLayout } from 'Shared/helpers/layout'
import './ModalPlayer.scss'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

class ModalPlayer extends Component {
  state = {
    isModalOpen: false,
    playTriggerPlayer: false
  }

  openPlayerModal = () => this.setState({ isModalOpen: true })
  closePlayerModal = () => this.setState({ isModalOpen: false })

  onTriggerPlay = () => {
    this.setState({ playTriggerPlayer: true })
    this.openPlayerModal()
    this.setState({ playTriggerPlayer: false })
  }

  getTrigger = () => {
    const { customTrigger, videoUrl, onReady, onError, width, height } = this.props

    switch (true) {
      case !!customTrigger:
        return <div onClick={this.onTriggerPlay}>
          {customTrigger}
        </div>
      default:
        return <ReactPlayer
          url={videoUrl}
          onPlay={this.onTriggerPlay}
          playing={this.state.playTriggerPlayer}
          width={width || 'auto'}
          height={height || 'auto'}
          onReady={onReady}
          onError={onError}
        />
    }
  }

  render () {
    const { videoUrl, isMobile, className } = this.props
    const portalClass = cx({ 'modal-player__portal': true, 'main--arabic': isArabicLayout })
    const width = isMobile ? window.innerWidth : 900
    const height = width * 0.57

    return (
      <Fragment>
        {this.getTrigger()}
        <ReactModal
          portalClassName={portalClass}
          overlayClassName='modal-player__overlay'
          bodyOpenClassName='modal-opened'
          isOpen={this.state.isModalOpen}
          contentLabel='Modal'
          className='modal-player'
          closeTimeoutMS={300}
          shouldCloseOnOverlayClick
          onRequestClose={this.closePlayerModal}
          ariaHideApp={false}
        >
          <div onClick={this.closePlayerModal} className='modal-player__close'>
            <i className='icon-im-close' />
          </div>
          <ReactPlayer
            className={className}
            url={videoUrl}
            width={`${width}px`}
            height={`${height}px`}
            playing
          />
        </ReactModal>
      </Fragment>
    )
  }
}

export default withDeviceWidthDetector(ModalPlayer)



