import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'
import FormUtilsService from 'Shared/services/FormUtilsService'
import { getProp } from 'Shared/helpers/object'
import * as R from 'ramda'

const formUtils = new FormUtilsService()

const getSkillsById = state => state.skills.candidate
const getPublicCandidateSkillsById = state => state.skills.publicCandidate

const getOrderedSkillsOptions = skills => {
  const userSkillsByOrder = transformToArray(skills)
    .sort((skillA, skillB) => {
      return getProp(skillA, 'CANDIDATESKILL').order - getProp(skillB, 'cANdidateSkill').order
    })
    .map(skill => (skill.translations[0]))

  return formUtils.mapCollectionToOptions(userSkillsByOrder, 'entityId', 'name')
}

export const getSkillsByUserLanguage = () =>
  createSelector(
    getSkillsById,
    (getSkillsById) => getOrderedSkillsOptions(getSkillsById)
  )

export const getPublicCandidateSkillsByUserLanguage = () =>
  createSelector(
    getPublicCandidateSkillsById,
    (getPublicCandidateSkillsById) => getOrderedSkillsOptions(getPublicCandidateSkillsById)
  )

export const getUserSkills = createSelector(
  getSkillsById,
  R.pipe(
    getOrderedSkillsOptions,
    R.map(
      R.propOr('', 'label')
    )
  )
)

export const hasCandidateAnySkills = createSelector(
  getSkillsById,
  R.pipe(
    transformToArray,
    R.isEmpty,
    R.not
  )
)



