import DeleteModalWeb from './DeleteModal'
import DeleteModalMobile from './DeleteModal.mobile'
import ConfirmModal from './ConfirmModal'
import React from 'react'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const DeleteModal = props => props.isMobile ? <DeleteModalMobile {...props} /> : <DeleteModalWeb {...props} />

export default withDeviceWidthDetector(DeleteModal)

export {
  ConfirmModal
}



