import FormUtilsService from 'Shared/services/FormUtilsService'
const formUtilsService = new FormUtilsService()

export const statuses = [
  {
    code: 'student_visa',
    translateKey: 'common.studentVisa'
  },
  {
    code: 'parent_visa',
    translateKey: 'common.parentVisa'
  },
  {
    code: 'spouse_visa',
    translateKey: 'common.spouseVisa'
  },
  {
    code: 'visit_visa',
    translateKey: 'common.visitVisa'
  },
  {
    code: 'emirati_visa',
    translateKey: 'common.emiratiVisa'
  },
  {
    code: 'gcc_citizen',
    translateKey: 'common.citizenVisa'
  },
  {
    code: 'employee_visa',
    translateKey: 'common.employeeVisa'
  }
]

export default statuses

export const visaStatusesOptions = () => formUtilsService.mapCollectionToOptions(statuses, 'code', 'translateKey', true)



