import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import WorkExperience from 'Candidate/forms/Profile/WorkExperience'
import { updateCandidate } from 'Shared/ducks/user/candidate'
import { scrollToFirstError } from 'Shared/helpers/scroll'

export const formName = 'CompleteProfile-HasWorkExperience'

let Form = reduxForm({
  form: formName,
  destroyOnUnmount: true,
  onSubmitFail: scrollToFirstError,
  enableReinitialize: true
})(WorkExperience)

const mapStateToProps = (state) => {
  return ({
    initialValues: {
      hasWorkExperience: state.user.details.hasWorkExperience
    }
  })
}

const mapDispatchToProps = (dispatch, props) => ({
  onFormSubmit: values => dispatch(updateCandidate(values))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form))



