import React from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import './EntitiesListTab.scss'

const EntitiesListTabWrapper = ({ children, light }) => {
  const wrapperClass = cx({
    'entities-list-tab__wrapper': true,
    'entities-list-tab__wrapper--light': light
  })

  return (
    <nav className={wrapperClass}>
      {children}
    </nav>
  )
}

EntitiesListTabWrapper.propTypes = {
  children: PropTypes.node.isRequired
}

export default EntitiesListTabWrapper



