export const setLocalData = (key, value) => {
  if (!value || !key) return false
  sessionStorage.setItem( key, JSON.stringify(value))
  return true
}

export const getLocalData = (value, key) => {
  if (!value) return false
  const data = JSON.parse(sessionStorage.getItem(value))
  return data
}

export const removeLocalData = (key) => {
  if (!key) return false
  sessionStorage.removeItem(key)
  return true
}

export const getSessionData = getLocalData
export const setSessionData = setLocalData
export const removeSessionData = removeLocalData



