import startCase from 'lodash/startCase'
import { MIN_SKILLS_COUNT } from 'Shared/constants/Experience'

export default values => {
  const errors = {}

  const requiredFields = ['skills']

  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = {
        key: 'errors.requiredField',
        options: {
          field: startCase(field)
        }
      }
    }
  })

  if (values['skills'].length < MIN_SKILLS_COUNT) {
    errors['skill'] = {
      key: 'errors.atLeastNSkills',
      options: {
        count: MIN_SKILLS_COUNT
      }
    }
  }

  return errors
}



