export const getRequest = (url, data = null, header) => {
  return new Promise((resolve, reject) => {
    const token = localStorage.getItem('token') //Token Optional as new 'CareerPro' APIs Token not required. 

    let headers = {
      'Content-Type': 'application/json',
      Accept: 'application/json',
      ...header
    }
    let q = ''
    if (token) {
      headers['Authorization'] = 'Bearer ' + token;
    }

    if (data) {
      let arr = []
      Object.keys(data).forEach((key) => {
        arr.push(`${key}=${data[key]}`)
      })
      q = arr.join('&')
    }

    try {
      fetch(`${url}${q && `?${q}`}`, {
        method: 'GET',
        mode: 'cors',
        headers,
      }).then((res) => {
        res.json().then((data) => {
          resolve(data)
        });
        return;
      });
    } catch (error) {
      reject(error)
      console.log('Error', error)
    }
  })
}

export const postRequest = (url, data = null, json = true, header) => {
  return new Promise((resolve, reject) => {
    const token = localStorage.getItem('token') // store?.getState()?.auth?.user?.token;
    // const subDomain = store?.getState()?.auth?.user?.username;
    let headers = {
      ...header,
      // "Content-Type": "application/json",
      Accept: 'application/json',
      // key: process.env.REACT_APP_OLIV_KEY
      // "Subdomain": "subDomain"
    }

    if (token) {
      headers['Authorization'] = 'Bearer ' + token
    }

    if (json) {
      data = JSON.stringify(data)
      headers['Content-Type'] = 'application/json'
    }

    try {
      fetch(url, {
        method: 'POST',
        mode: 'cors',
        headers,
        body: data
      }).then((res) => {
        res.json().then((data) => {
          resolve(data)
        })
        return
      })
    } catch (error) {
      reject(error)
      console.log('Error', error)
      return
    }
  });
}



