import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Form, Field } from 'redux-form'
import withTranslations from 'Shared/hoc/withTranslations'
import { Input, Select } from 'Shared/components/FormElements'
import Button, { PrimaryButtonOutline } from 'Shared/components/Button'
import NotificationService from 'Shared/services/NotificationService'

class CertificatesDetails extends Component {
  static propTypes = {
    closeOnSubmit: PropTypes.func.isRequired,
    handleDelete: PropTypes.func.isRequired,
    saveCertificate: PropTypes.func.isRequired
  }

  _onFormSubmit = values => {
    this.props.saveCertificate(values)
      .then(() => this.props.closeOnSubmit())
  }

  handleDelete = () => this.props.disableDelete ? NotificationService.error(this.props.t('alerts.certificateDeleteDisabled')) : this.props.handleDelete()

  render () {
    const {
      t,
      handleSubmit,
      years
    } = this.props

    return (
      <Form onSubmit={handleSubmit(this._onFormSubmit)}>
        <Field
          name='name'
          component={Input}
          label={t('forms.courseCertificateName.label')}
          placeholder={t('forms.courseCertificateName.placeholder')}
        />
        <Field
          name='issuer'
          component={Input}
          label={t('forms.organisation.label')}
          placeholder={t('forms.organisation.placeholder')}
        />
        <Field
          name='issuedAt'
          component={Select}
          label={t('forms.yearOfCompletion.label')}
          options={years}
          withIcon
        />
        <Field
          component={Input}
          name='certificateId'
          type='hidden'
        />
        <div className='form__wrapper--button-group'>
          <PrimaryButtonOutline type='submit'>{t('common.save')}</PrimaryButtonOutline>
          <Button justIcon onClick={this.handleDelete}><i className='ion-md-trash' /></Button>
        </div>
      </Form>
    )
  }
}

export default withTranslations(CertificatesDetails)



