import findKey from 'lodash/findKey'
import * as R from 'ramda'

/**
 * Returns property from object by case insensitive key
 * @param obj Object
 * @param name String
 * @returns {*}
 */
export const getProp = (obj, name) => {
  const realName = findKey(obj, (value, key) => key.toLocaleLowerCase() === name.toLocaleLowerCase())
  return obj[realName]
}

export const areValuesEmpty = R.pipe(
  R.values,
  R.any(R.isNil)
)

export const omitWhen = R.curry((predicate, propList, object) =>
  R.mergeRight(
    R.omit(propList, object),
    R.reject(predicate, R.pick(propList, object)))
)



