import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Field } from 'redux-form'
import HighSchoolAlgoliaComponentConnector from 'Candidate/connectors/Forms/Profile/Education/HighSchoolAlgoliaComponentConnector'
import withTranslations from 'Shared/hoc/withTranslations/index'

class HighSchoolAlgoliaField extends Component {
  static propTypes = {
    onSelect: PropTypes.func.isRequired
  }

  render () {
    const { t, name } = this.props
    return (
      <Field
        component={HighSchoolAlgoliaComponentConnector}
        defaultRefinement={this.props.defaultRefinement}
        label={t('forms.highSchoolName.label')}
        placeholder={t('forms.highSchoolName.placeholder')}
        name={name}
        onSelect={this.props.onSelect}
        simpleValue
      />

    )
  }
}

HighSchoolAlgoliaField.defaultProps = {
  name: 'highSchoolId'
}

export default withTranslations(HighSchoolAlgoliaField)



