import moment from 'moment'
import { pipe, prop, reduce, ifElse, propSatisfies, isNil, add, filter, allPass, propEq, not } from 'ramda'
import { experienceTypes } from 'Shared/constants/Experience'

const diffInYearsUntilNow = date => moment().diff(moment(date), 'years', true)
const workExperienceDurationInYears = experience => moment(experience.finishedAt).diff(moment(experience.startedAt), 'years', true)

/**
 * Sums candidate's years of experience; if `finishedAt` is null, we assume he/she is still working there
 */

export const sumYearsOfExperience = pipe(
  reduce(
    (acc, experience) => ifElse(
      propSatisfies(isNil, 'finishedAt'),
      pipe(
        prop('startedAt'),
        diffInYearsUntilNow,
        add(acc)
      ),
      pipe(
        workExperienceDurationInYears,
        add(acc)
      )
    )(experience),
    0
  )
)

export const filterCandidatesExperienceByEmployment = userId => filter(
  allPass([
    propEq('candidateId', userId),
    propEq('type', experienceTypes.employment)
  ])
)

export const filterCandidatesExperienceByVolunteering = userId => filter(
  allPass([
    propEq('candidateId', userId),
    pipe(
      propEq('type', experienceTypes.employment),
      not
    )
  ])
)



