import * as R from 'ramda'
import { SALARY_RANGE } from 'Shared/constants/salaryRange'

const getNullIfUndisclosed = R.ifElse(
  R.equals(SALARY_RANGE.notSure),
  R.always(null),
  R.identity
)

export const prepareSalaryRangeValue = R.ifElse(
  R.propEq('salaryRange', SALARY_RANGE.graduateWage),
  R.prop('graduateWage'),
  R.pipe(
    R.prop('salaryRange'),
    getNullIfUndisclosed
  )
)

const graduateWageInclude = value => R.includes(value, [SALARY_RANGE.graduateWage1, SALARY_RANGE.graduateWage2, SALARY_RANGE.graduateWage3])

const getUndisclosedIfNull = R.ifElse(
  R.equals(null),
  R.always(SALARY_RANGE.notSure),
  R.identity
)

export const getSalaryRangeValue = R.ifElse(
  graduateWageInclude,
  R.always(SALARY_RANGE.graduateWage),
  getUndisclosedIfNull
)



