import { capitalizeWords } from './string'

export const getRequestTimeSlotChangeDefaultMessage = firstName => {
  return (
    ` Hi there,

Thank you so much for reaching out. Unfortunately I am not able to attend an interview during the suggested times.

Would it be possible for us to reschedule? I'd love to have an opportunity to discuss my application with you.

Best regards,
${capitalizeWords(firstName)}`
  )
}



