import { getRequest } from './commonApi'
import { endPoints } from './endPoints'
import { setLocalData } from 'Shared/helpers/session'

const useApiGetCities = () => {
  const apiGetCities = async (selectedCountry) => {
    const url = endPoints.getCities
    const data = selectedCountry ? { country: selectedCountry } : null

    try {
      let res = await getRequest(url, data)
      if (res.success) {
        setLocalData('cities', res.data)
        return res.data
      }
    } catch (err) {
      console.log('Err', err)
    }
  }

  return { apiGetCities }
}

const apiCitySearch = async (url, data) => {
  url = endPoints.getCities
  try {
    let res = await getRequest(url, data)
    if (res.success) {
      return res.data
    }
  } catch (err) {
    console.log('Err', err)
  }
}

export default useApiGetCities
export { useApiGetCities, apiCitySearch }



