import startCase from 'lodash/startCase'
import trimEnd from 'lodash/trimEnd'

export default values => {
  const errors = {}

  const requiredFields = ['degree', 'gradeFormat', 'gradeSystem', 'gradeValue', 'graduationMonth', 'graduationYear']

  const requiredFieldsId = ['majorId', 'universityId']

  const phoneNumberValidation = /^[0-9()\s+]+$/

  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = {
        key: 'errors.requiredField',
        options: {
          field: startCase(field)
        }
      }
    }
  })

  requiredFieldsId.forEach(field => {
    if (!values[field]) {
      errors[field] = {
        key: 'errors.requiredField',
        options: {
          field: trimEnd(field, 'Id')
        }
      }
    }
  })

  if (values.phoneNumber && !phoneNumberValidation.test(values.phoneNumber)) {
    errors.phoneNumber = {
      key: 'errors.invalidPhoneNumber',
      options: {}
    }
  }

  return errors
}



