import { connect } from 'react-redux'
import { reduxForm } from 'redux-form'
import CertificateDetailsForm from 'Candidate/forms/Profile/CertificateDetailsForm'
import validate from './validateCertificates'
import { generatePastYearsList } from 'Shared/helpers/date'
import FormUtilsService from 'Shared/services/FormUtilsService'
import { addCertificate } from 'Shared/ducks/certificates'
import { scrollToFirstError } from 'Shared/helpers/scroll'

export const formName = 'CompleteProfile-AddNewCertificate'

const formUtilsService = new FormUtilsService()

let Form = reduxForm({
  form: formName,
  validate,
  onSubmitFail: scrollToFirstError,
  enableReinitialize: true
})(CertificateDetailsForm)

const mapStateToProps = state => ({
  years: formUtilsService.mapArrayToOptions(generatePastYearsList())
})

const mapDispatchToProps = dispatch => ({
  saveCertificate: (values) => dispatch(addCertificate(values))
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)



