import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'
import filter from 'lodash/filter'
import { experienceTypes } from 'Shared/constants/Experience'
import { getUserId } from 'Shared/selectors/user'
import * as R from 'ramda'
import { formatDate } from 'Shared/helpers/date'
import { formats } from 'Shared/constants/Date'
import {
  sumYearsOfExperience,
  filterCandidatesExperienceByEmployment,
  filterCandidatesExperienceByVolunteering
} from 'Shared/helpers/experience'
import { t } from 'i18next'

export const getExperienceById = state => state.experiences.entries

const getDateProp = R.ifElse(
  R.pipe(
    R.prop('finishedAt'),
    R.isNil
  ),
  R.prop('startedAt'),
  R.prop('finishedAt')
)

export const getExperiencesByCandidateId = id =>
  createSelector(
    getExperienceById,
    getExperienceById => {
      const getExperiencesArray = filter(transformToArray(getExperienceById), {'type': experienceTypes.employment, candidateId: id})

      return R.reverse(R.sortBy(getDateProp, getExperiencesArray))
    }
  )

export const getVolunteeringByCandidateId = id =>
  createSelector(
    getExperienceById,
    getExperienceById => {
      const getExperiencesArray = transformToArray(getExperienceById)
        .filter(experience => experience.type !== experienceTypes.employment && experience.candidateId === id)

      return R.reverse(R.sortBy(getDateProp, getExperiencesArray))
    }
  )

export const getUserExperience = createSelector(
  getExperienceById,
  getUserId,
  (experience, userId) => {
    return R.pipe(
      transformToArray,
      filterCandidatesExperienceByEmployment(userId),
      R.sortBy(getDateProp),
      R.reverse
    )(experience)
  }
)

export const getUserVolunteering = createSelector(
  getExperienceById,
  getUserId,
  (experience, userId) => {
    return R.pipe(
      transformToArray,
      filterCandidatesExperienceByVolunteering(userId),
      R.sortBy(getDateProp),
      R.reverse
    )(experience)
  }
)

export const getExperienceDetails = createSelector(
  getExperienceById,
  (state, id) => id,
  (experiences, id) => R.propOr({}, id)(experiences)
)

export const getExperiencePosition = createSelector(
  getExperienceById,
  (state, id) => id,
  (experiences, id) => R.pathOr('', [id, 'position'])(experiences)
)

export const getExperienceOrganisationName = createSelector(
  getExperienceById,
  (state, id) => id,
  (experiences, id) => R.pipe(
    R.propOr({}, id),
    R.ifElse(
      R.hasPath(['organisation', 'name']),
      R.pathOr('', ['organisation', 'name']),
      R.prop('organisationName')
    )
  )(experiences)
)

const formatByDayYear = date => formatDate(date, formats.dayYear)

export const getExperienceDates = createSelector(
  getExperienceById,
  (state, id) => id,
  (experiences, id) => R.pipe(
    R.propOr({}, id),
    R.applySpec({
      startedAt: R.pipe(
        R.propOr('', 'startedAt'),
        formatByDayYear
      ),
      finishedAt: R.pipe(
        R.propOr(null, 'finishedAt'),
        R.ifElse(
          R.isNil,
          R.always(t('common.current')),
          formatByDayYear
        )
      )
    }),
    R.values,
    R.join(' - ')
  )(experiences)
)

export const getExperienceDescription = createSelector(
  getExperienceById,
  (state, id) => id,
  (experiences, id) => R.pathOr('', [id, 'description'])(experiences)
)

export const hasOneWorkExperience = createSelector(
  getExperienceById,
  getUserId,
  (experiences, userId) => R.pipe(
    transformToArray,
    filterCandidatesExperienceByEmployment(userId),
    R.length,
    R.equals(1)
  )(experiences)
)

export const hasOneVolunteering = createSelector(
  getExperienceById,
  getUserId,
  (experiences, userId) => R.pipe(
    transformToArray,
    filterCandidatesExperienceByVolunteering(userId),
    R.length,
    R.equals(1)
  )(experiences)
)

export const getCandidateExperience = createSelector(
  getExperienceById,
  (state, id) => id,
  (experience, userId) => {
    return R.pipe(
      transformToArray,
      filterCandidatesExperienceByEmployment(userId),
      R.sortBy(getDateProp),
      R.reverse
    )(experience)
  }
)

export const getCandidateVolunteering = createSelector(
  getExperienceById,
  (state, id) => id,
  (experience, userId) => {
    return R.pipe(
      transformToArray,
      filterCandidatesExperienceByVolunteering(userId),
      R.sortBy(getDateProp),
      R.reverse
    )(experience)
  }
)

export const hasCandidateAnyExperience = createSelector(
  getExperienceById,
  (state, id) => id,
  (experiences, userId) => R.pipe(
    transformToArray,
    filterCandidatesExperienceByEmployment(userId),
    R.isEmpty,
    R.not
  )(experiences)
)

export const hasCandidateAnyVolunteering = createSelector(
  getExperienceById,
  (state, id) => id,
  (experiences, userId) => R.pipe(
    transformToArray,
    filterCandidatesExperienceByVolunteering(userId),
    R.isEmpty,
    R.not
  )(experiences)
)

export const hasMoreThan6YearsOfExperience = createSelector(
  getExperienceById,
  (state, id) => id,
  (experiences, userId) => R.pipe(
    transformToArray,
    filterCandidatesExperienceByEmployment(userId),
    sumYearsOfExperience,
    R.lte(6)
  )(experiences)
)



