import React, { useEffect } from 'react'
import './SalaryExpectationFields.scss'
import { Field } from 'redux-form'
import { Radio, RadioWrapper } from 'Shared/components/FormElements'
import { t } from 'i18next'
import * as R from 'ramda'
import { SALARY_RANGE } from 'Shared/constants/salaryRange'

function SalaryExpectationFields (props) {
  useEffect(() => {
    props.fetchSalaryRanges()
  }, [])

  const generateLabel = (label, description) => (
    <span>
      <strong>{label}</strong> - {description}
    </span>
  )

  const showGraduateWageOptions = () => R.pipe(
    R.pathOr('', ['selectedValues', 'salaryRange']),
    R.equals(SALARY_RANGE.graduateWage)
  )(props)

  return (
    <div className='form__group salary-expectation-fields__container'>
      <RadioWrapper
        layout='checkbox-vertical'
        name='salaryRange'
      >
        <Field
          component={Radio}
          text={generateLabel(t('forms.salaryExpectations.experienceOnly.label'), t('forms.salaryExpectations.experienceOnly.description'))}
          description={t('forms.salaryExpectations.experienceOnly.details', { amount: props.experienceRange })}
          name='salaryRange'
          radioButtonValue={SALARY_RANGE.experienceOnly}
        />
        <Field
          component={Radio}
          text={generateLabel(t('forms.salaryExpectations.expenseCoverage.label'), t('forms.salaryExpectations.expenseCoverage.description'))}
          description={t('forms.salaryExpectations.expenseCoverage.details', { amount: props.expenseCoverageRange })}
          name='salaryRange'
          radioButtonValue={SALARY_RANGE.expenseCoverage}
        />
        <Field
          component={Radio}
          text={generateLabel(t('forms.salaryExpectations.graduateWage.label'), t('forms.salaryExpectations.graduateWage.description'))}
          name='salaryRange'
          radioButtonValue={SALARY_RANGE.graduateWage}
        />
        {
          showGraduateWageOptions() && (
            <fieldset className='salary-expectation-fields__sub-fieldset'>
              <Field
                component={Radio}
                text={props.gradWage1Range}
                name='graduateWage'
                radioButtonValue={SALARY_RANGE.graduateWage1}
              />
              <Field
                component={Radio}
                text={props.gradWage2Range}
                name='graduateWage'
                radioButtonValue={SALARY_RANGE.graduateWage2}
              />
              <Field
                component={Radio}
                text={props.gradWage3Range}
                name='graduateWage'
                radioButtonValue={SALARY_RANGE.graduateWage3}
              />
            </fieldset>
          )
        }
        <Field
          component={Radio}
          text={generateLabel(t('forms.salaryExpectations.notSure.label'), t('forms.salaryExpectations.notSure.description'))}
          name='salaryRange'
          radioButtonValue={SALARY_RANGE.notSure}
        />
      </RadioWrapper>
    </div>
  )
}

export default SalaryExpectationFields



