import { reduxForm, formValueSelector, change } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import VolunteeringDetails from 'Candidate/forms/Profile/VolunteeringDetails'
import { editExperienceById } from 'Shared/ducks/experiences'
import validate from './VolunteeringFormValidator'
import { activityOptions } from 'Shared/constants/Experience'

export const formName = 'CompleteProfile-VolunteeringDetails'

let Form = reduxForm({
  form: formName,
  destroyOnUnmount: true,
  enableReinitialize: true,
  validate
})(VolunteeringDetails)

const mapStateToProps = (state, props) => {
  const dynamicFormName = `${formName}-${props.details.id}`

  return ({
    form: dynamicFormName,
    activityTypes: activityOptions(),
    currentWork: formValueSelector(dynamicFormName)(state, 'currentWork'),
    initialValues: {
      ...props.details,
      experienceId: props.details.id,
      currentWork: !props.details.finishedAt
    }
  })
}

const mapDispatchToProps = (dispatch, props) => ({
  onFormSubmit: values => dispatch(editExperienceById(values)),
  onCurrentWorkChange: () => dispatch(change(`${formName}-${props.details.id}`, 'finishedAt', null))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form))



