import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Field } from 'redux-form'
import UniversityAlgoliaComponentConnector from 'Candidate/connectors/Forms/Profile/Education/UniversityAlgoliaComponentConnector'
import withTranslations from 'Shared/hoc/withTranslations/index'

class UniveristyAlgoliaField extends Component {
  static propTypes = {
    onSelect: PropTypes.func.isRequired
  }

  render () {
    const { t, name } = this.props
    return (
      <Field
        component={UniversityAlgoliaComponentConnector}
        defaultRefinement={this.props.defaultRefinement}
        label={t('forms.universityName.label')}
        placeholder={t('forms.universityName.placeholder')}
        name={name}
        onSelect={this.props.onSelect}
        simpleValue
      />

    )
  }
}

UniveristyAlgoliaField.defaultProps = {
  name: 'universityId'
}

export default withTranslations(UniveristyAlgoliaField)



