import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { submit, hasSubmitSucceeded, isSubmitting } from 'redux-form'
import {
  getApplicationTimeSlots,
  isAnyTimeSlotChosen,
  chosenTimeSlotId
} from 'Shared/selectors/jobOfferApplicationTimeSlots'
import {
  startApplicationTimeSlotsLoading,
  finishApplicationTimeSlotsLoading,
  selectApplicationTimeSlot
} from 'Shared/ducks/jobOfferApplicationTimeSlots'
import { sendMessage } from 'Chat/ducks/classes/messages'
import TimeSlotsSelection from 'Chat/components/TimeSlotsSelection'
import { formName as RequestTimeSlotChangeFormName } from 'Chat/connectors/Forms/RequestTimeSlotChange/RequestTimeSlotChangeFormConnector'

const mapStateToProps = (state, ownProps) => ({
  timeSlots: getApplicationTimeSlots(state, ownProps.applicationId),
  isAnyTimeSlotChosen: isAnyTimeSlotChosen(state, ownProps.applicationId),
  chosenTimeSlotId: chosenTimeSlotId(state, ownProps.applicationId),
  hasRequestChangeTimeSlotsSubmitSucceeded: hasSubmitSucceeded(RequestTimeSlotChangeFormName)(state),
  isRequestChangeTimeSlotsFormSubmitting: isSubmitting(RequestTimeSlotChangeFormName)(state),
  userName: state.user.details.firstName
})

const mapDispatchToProps = (dispatch, ownProps) => {
  const channelSid = ownProps.match.params.channelSid || ownProps.channelSid

  return ({
    submitRequestChangeTimeSlots: () => dispatch(submit(RequestTimeSlotChangeFormName)),
    setTimeSlotAsChosen: async (timeSlotId, message) => {
      try {
        dispatch(startApplicationTimeSlotsLoading())
        dispatch(selectApplicationTimeSlot(timeSlotId, channelSid))
        await dispatch(sendMessage(channelSid, message))
        return dispatch(finishApplicationTimeSlotsLoading())
      } catch (err) {
        dispatch(finishApplicationTimeSlotsLoading())
        return console.error(err)
      }
    }
  })
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(TimeSlotsSelection))



