import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Field, Form } from 'redux-form'
import moment from 'moment'
import {
  Editor,
  Input,
  InputDatePicker,
  Select,
  RadioWrapper,
  Checkbox
} from 'Shared/components/FormElements'
import './EducationDetails.scss'
import Button, { PrimaryButtonOutline } from 'Shared/components/Button'
import { lengthLimit } from 'Shared/constants/Editor'
import withTranslations from 'Shared/hoc/withTranslations'
import { cleanTrailingSpacesFromHtml } from 'Shared/helpers/string'
import NotificationService from 'Shared/services/NotificationService'

class VolunteeringDetails extends Component {
  static propTypes = {
    handleDelete: PropTypes.func.isRequired,
    closeOnSubmit: PropTypes.func.isRequired,
    onFormSubmit: PropTypes.func.isRequired,
    onCurrentWorkChange: PropTypes.func.isRequired,
    activityTypes: PropTypes.array.isRequired,
    currentWork: PropTypes.bool.isRequired
  }

  componentWillReceiveProps (nextProps) {
    if (this.props.currentWork !== nextProps.currentWork) {
      nextProps.currentWork && this.props.onCurrentWorkChange()
    }
  }

  _onFormSubmit = values => {
    const cleanValues = { ...values, description: cleanTrailingSpacesFromHtml(values.description) }
    this.props.onFormSubmit(cleanValues)
      .then(() => this.props.closeOnSubmit())
  }

  handleDelete = () => this.props.disableDelete ? NotificationService.error(this.props.t('alerts.volunteeringDeleteDisabled')) : this.props.handleDelete()

  render () {
    const {
      t,
      currentWork,
      activityTypes,
      handleSubmit
    } = this.props
    return (
      <Form onSubmit={handleSubmit(this._onFormSubmit)}>
        <div className='form__group'>
          <Field
            component={Select}
            label={t('forms.activityType.label')}
            options={activityTypes}
            placeholder={t('forms.activityType.placeholder')}
            name='type'
            withIcon
          />
          <Field
            component={Input}
            label={t('forms.organisationName.label')}
            placeholder={t('forms.organisationName.placeholder')}
            name='organisationName'
            simpleValue
          />
          <Field
            component={Input}
            label={t('forms.role.label')}
            name='position'
            type='text'
            placeholder={t('forms.role.placeholder')}
          />
          <div className='form__group'>
            <div className='columns'>
              <div className='column col-6 col-xs-12'>
                <Field
                  component={InputDatePicker}
                  label={t('forms.startDate.label')}
                  name='startedAt'
                  icon={'ion-md-clock'}
                  maxDate={moment().utc()}
                  floatRight
                  scrollableYearDropdown
                  showYearDropdown
                  showMonthDropdown
                  placeholderText={t('forms.startDate.placeholder')}
                  format={(value, name) => value ? moment(value, moment.ISO_8601) : null}
                />
              </div>
              <div className='column col-6 col-xs-12'>
                <Field
                  component={InputDatePicker}
                  label={t('forms.endDate.label')}
                  name='finishedAt'
                  icon={'ion-md-clock'}
                  maxDate={moment().utc()}
                  floatRight
                  scrollableYearDropdown
                  showYearDropdown
                  showMonthDropdown
                  placeholderText={currentWork ? t('common.present') : t('forms.endDate.placeholder')}
                  format={(value, name) => value ? moment(value, moment.ISO_8601) : null}
                  disabled={currentWork}
                />
              </div>
            </div>
            <RadioWrapper>
              <Field
                component={Checkbox}
                text={t('profile.experience.sections.volunteering.iCurrentlyVolunteerHere')}
                name='currentWork'
                normalize={v => !!v}
              />
            </RadioWrapper>
          </div>
          <div className='form__group'>
            <Field
              component={Editor}
              label={t('common.description')}
              name='description'
              limit={lengthLimit.description}
              placeholder={t('profile.experience.sections.workExperience.description')}
            />
            <Field
              component={Input}
              name='experienceId'
              type='hidden'
            />
            <Field
              component={Input}
              name='candidateId'
              type='hidden'
            />
          </div>
          <div className='form__wrapper--button-group'>
            <PrimaryButtonOutline type='submit'>{t('common.save')}</PrimaryButtonOutline>
            <Button onClick={this.handleDelete} justIcon><i className='ion-md-trash' /></Button>
          </div>
        </div>
      </Form>
    )
  }
}

export default withTranslations(VolunteeringDetails)



