import { lengthLimit } from 'Shared/constants/Editor'
import startCase from 'lodash/startCase'
const [min, max] = lengthLimit.requestChangeTimeSlot

export default values => {
  const errors = {}

  if (values['message'] && values['message'].length < min) {
    errors['message'] = {
      key: 'errors.textFieldTooShort',
      options: {
        field: startCase('message'),
        chars: startCase(min.toString())
      }
    }
  }

  if (values['message'] && values['message'].length > max) {
    errors['message'] = {
      key: 'errors.textFieldTooLong',
      options: {
        field: startCase('message'),
        chars: startCase(max.toString())
      }
    }
  }

  if (!values['message']) {
    errors['message'] = {
      key: 'errors.requiredField',
      options: {
        field: startCase('message')
      }
    }
  }

  return errors
}



