import { connect } from 'react-redux'
import SalaryExpectationFields from 'Candidate/forms/SalaryExpectationFields/SalaryExpectationFields'
import { fetchSalaryRanges } from 'Shared/ducks/salaryRange'
import { getExperienceRange, getExpenseCoverageRange, getGradWage1Range, getGradWage2Range, getGradWage3Range } from 'Shared/selectors/salaryRange'

const mapStateToProps = (state, ownProps) => ({
  experienceRange: getExperienceRange(state, ownProps.selectedCurrency),
  expenseCoverageRange: getExpenseCoverageRange(state, ownProps.selectedCurrency),
  gradWage1Range: getGradWage1Range(state, ownProps.selectedCurrency),
  gradWage2Range: getGradWage2Range(state, ownProps.selectedCurrency),
  gradWage3Range: getGradWage3Range(state, ownProps.selectedCurrency)
})

const mapDispatchToProps = dispatch => ({
  fetchSalaryRanges: () => dispatch(fetchSalaryRanges())
})

export default connect(mapStateToProps, mapDispatchToProps)(SalaryExpectationFields)



