import React, { Component } from 'react'
import PropTypes from 'prop-types'
import ReactModal from 'react-modal'
import cx from 'classnames'
import './MobileWindow.scss'

class MobileWindow extends Component {
  componentDidMount () {
    this.props.show && this.disableBodyScroll()
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (prevProps.show !== this.props.show && this.props.show) {
      this.disableBodyScroll()
    }

    if (prevProps.show !== this.props.show && !this.props.show) {
      this.enableBodyScroll()
    }
  }

  disableBodyScroll = () => {
    document.getElementsByTagName('body')[0].style.overflow = 'hidden'
  }
  enableBodyScroll = () => {
    document.getElementsByTagName('body')[0].style.overflow = 'auto'
  }

  render () {
    const { children, show, saveInDom } = this.props
    // saveInDom is added because Algolia filters needs to be in DOM to have the search triggered //Sandra 25.02.2019

    const modalClass = cx({
      'mobile-window': true,
      'save-in-dom--show': saveInDom && show,
      'save-in-dom--hide': saveInDom && !show
    })

    const portalClass = cx({
      'mobile-window__portal': true,
      'save-in-dom--show': saveInDom && show,
      'save-in-dom--hide': saveInDom && !show
    })

    return (
      <ReactModal
        isOpen={saveInDom ? true : show}
        contentLabel='Modal'
        closeTimeoutMS={300}
        shouldCloseOnOverlayClick
        onRequestClose={() => {}}
        className={modalClass}
        portalClassName={portalClass}
      >
        <div>
          {children}
        </div>
      </ReactModal>
    )
  }
}

MobileWindow.propTypes = {
  children: PropTypes.node.isRequired,
  title: PropTypes.string,
  cancelText: PropTypes.string,
  submitText: PropTypes.string,
  handleClose: PropTypes.func,
  handleCancel: PropTypes.func,
  handleSubmit: PropTypes.func,
  show: PropTypes.bool,
  showCancel: PropTypes.bool,
  showSubmit: PropTypes.bool,
  loading: PropTypes.bool,
  disableSubmit: PropTypes.bool
}

MobileWindow.defaultProps = {
  show: false,
  showCancel: true,
  showSubmit: true,
  closeOnSubmit: true,
  closeOnCancel: true
}

export default MobileWindow



