import React, { Component } from 'react'
import { Element } from 'react-scroll'
import DatePicker from 'react-datepicker'
import cx from 'classnames'
import PropTypes from 'prop-types'
import moment from 'moment'
import pick from 'lodash/pick'
import withTranslations from 'Shared/hoc/withTranslations'
import Label from 'Shared/components/FormElements/Label'

class CustomDatePicker extends DatePicker {
  deferFocusInput = () => {
    this.cancelFocusInput()
  }
}

class InputDatePicker extends Component {
  state = {
    calendarFocused: false
  }

  focusDatepicker = () => this.setState({ calendarFocused: true })
  blurDatepicker = () => this.setState({ calendarFocused: false })

  _onBlurHandler = () => {
    const { input } = this.props
    this.blurDatepicker()
    input.onBlur(input.value)
  }

  onChange = value => {
    this.props.input.onChange(value)
  }

  getLabel = () => {
    const { t, tooltip, label, meta: { touched, error } } = this.props

    switch (true) {
      case !!label && !tooltip:
        return <Label>{touched && error ? t(error.key, error.options) : label}</Label>
      case !!label && !!tooltip:
        return (
          <div className='form__group--datepicker-with-tooltip'>
            <Label>{touched && error ? t(error.key, error.options) : label}</Label>
            {tooltip}
          </div>
        )
      default:
        return null
    }
  }

  render () {
    const { input, label, t, floatRight, meta: { touched, error, submitting }, className, dateFormat, ...props } = this.props

    const fieldClass = cx({
      'form__group form__group--with-icons form__group--datepicker': true,
      'form__group--has-error': !submitting && touched && error,
      'form__group--float-right': floatRight,
      'form__group--datepicker-focused': this.state.calendarFocused,
      [className]: !!className
    })

    const datePickerClass = cx({
      'form__input form__input--datepicker': true,
      'form__input--disabled': props.disabled
    })

    const datePickerProps = pick(props, [
      'disabled',
      'minDate',
      'maxDate',
      'placeholderText',
      'yearDropdownItemNumber',
      'scrollableYearDropdown',
      'showYearDropdown',
      'showMonthDropdown',
      'selectsStart',
      'selectsEnd',
      'startDate',
      'endDate',
      'showTimeSelect',
      'showTimeSelectOnly',
      'dateFormat',
      'timeCaption'
    ])

    return (
      <div className={fieldClass}>
        {input.name && <Element name={`error-position-${input.name}`} />}
        {this.getLabel()}
        <div className={'input__date-picker'}>
          <CustomDatePicker
            {...input}
            autocomplete='off'
            dateFormat={dateFormat}
            onChange={this.onChange}
            className={datePickerClass}
            onSelect={this.focusDatepicker}
            onFocus={this.focusDatepicker}
            onBlur={this._onBlurHandler}
            onClickOutside={this.blurDatepicker}
            popperPlacement='bottom-end'
            value={input.value ? moment.utc(input.value).format(dateFormat) : null}
            selected={input.value ? moment.utc(input.value) : null}
            {...datePickerProps}
            utcOffset={0}
          />
          <i className='form__input__icon ion-md-calendar' />
        </div>
      </div>
    )
  }
}

InputDatePicker.propTypes = {
  disabled: PropTypes.bool,
  minDate: PropTypes.any,
  maxDate: PropTypes.any,
  placeholderText: PropTypes.string,
  yearDropdownItemNumber: PropTypes.number,
  scrollableYearDropdown: PropTypes.bool,
  showYearDropdown: PropTypes.bool,
  showMonthDropdown: PropTypes.bool,
  className: PropTypes.string,
  dateFormat: PropTypes.string
}

InputDatePicker.defaultProps = {
  dateFormat: 'LL'
}

export default withTranslations(InputDatePicker)



