import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Field, Form } from 'redux-form'
import withTranslations from 'Shared/hoc/withTranslations'
import { Select } from 'Shared/components/FormElements'
import Button, { PrimaryButtonOutline } from 'Shared/components/Button'
import HighSchoolAlgoliaField from 'Candidate/components/HighSchoolAlgoliaField'
import './EducationDetails.scss'
import NotificationService from 'Shared/services/NotificationService'

class EducationHighSchoolDetails extends Component {
  componentDidMount () {
    this.props.fetchHighSchoolEducationGradingSystem()
  }

  _onFormSubmit = values => {
    this.props.onFormSubmit(this.props.candidateId, values)
      .then(this.props.closeOnSubmit)
  }

  handleDelete = () => {
    this.props.disableDelete ? NotificationService.error(this.props.t('alerts.highSchoolDeleteDisabled')) : this.props.handleDelete()
  }

  render () {
    const { t, handleSubmit } = this.props

    return (
      <Form onSubmit={handleSubmit(this._onFormSubmit)}>
        <div className='step_education_body__form'>
          <HighSchoolAlgoliaField
            onSelect={this.props.chooseHighSchool}
            defaultRefinement={this.props.highSchoolName}
          />
          <Field
            component={Select}
            options={this.props.gradingSystemOptions}
            label={t('forms.educationSystem.label')}
            placeholder={t('forms.educationSystem.placeholder')}
            name='educationSystem'
            withIcon
          />
          <div className='columns form__wrapper--inline'>
            <div className='column col-8 col-xs-12'>
              <Field
                component={Select}
                options={this.props.graduationMonths}
                label={t('forms.graduationMonth.label')}
                placeholder={t('forms.graduationMonth.placeholder')}
                name='graduationMonth'
                withIcon
              />
            </div>
            <div className='column col-4 col-xs-12'>
              <Field
                component={Select}
                options={this.props.graduationYears}
                label={t('forms.graduationYear.label')}
                placeholder={t('forms.graduationYear.placeholder')}
                name='graduationYear'
                withIcon
              />
            </div>
          </div>
        </div>
        <div className='form__wrapper--button-group'>
          <PrimaryButtonOutline type='submit'>{t('common.save')}</PrimaryButtonOutline>
          <Button onClick={this.handleDelete} justIcon><i className='ion-md-trash' /></Button>
        </div>
      </Form>
    )
  }
}

EducationHighSchoolDetails.propTypes = {
  closeOnSubmit: PropTypes.func.isRequired,
  chooseHighSchool: PropTypes.func.isRequired,
  graduationYears: PropTypes.array.isRequired,
  handleDelete: PropTypes.func.isRequired,
  fetchHighSchoolEducationGradingSystem: PropTypes.func.isRequired,
  candidateId: PropTypes.string.isRequired,
  gradingSystemOptions: PropTypes.array
}

export default withTranslations(EducationHighSchoolDetails)



