import React, { Component } from 'react'
import { Field, Form } from 'redux-form'
import withTranslations from 'Shared/hoc/withTranslations'
import { Input } from 'Shared/components/FormElements'
import Button, { BigJustIconButton, PrimaryButton } from 'Shared/components/Button'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import { isFileTitleTaken } from 'Shared/helpers/files'
import NotificationService from 'Shared/services/NotificationService'
import cx from 'classnames'

const R = require('ramda')

class UploadedFileForm extends Component {
  state = {
    hasNameError: false,
    fileName: '',
    editMode: this.props.defaultEditMode
  }

  updateFile = async () => {
    if (isFileTitleTaken(this.props.files, { name: this.state.fileName })) {
      this.setState({ hasNameError: true })
      return NotificationService.info(this.props.t('alerts.fileExists'))
    }

    await this.props.submit()
    this.turnEditModeOff()
    return this.props.afterSubmit()
  }

  onFileNameChange = event => {
    this.setState({ hasNameError: false, fileName: event.target.value })
  }

  turnEditModeOn = () => this.setState({ editMode: true })
  turnEditModeOff = () => this.setState({ editMode: false })

  render () {
    const { t } = this.props
    const { editMode } = this.state
    // Hack for no parent selector in css (tracking field meta and adjusting margin, on lower resolutions it is done by css)
    const isError = !!(window.innerWidth > 767 && this.props.formErrors.documentTitle && R.path(['documentTitle', 'touched'], this.props.formMeta))
    const inputClassName = cx({
      'form__links-group--input hidden__label': true,
      'file-input__cv': true,
      'form__links-group--input-error': this.state.hasNameError
    })

    return (
      <Form onSubmit={this.props.handleSubmit}>
        <div className='form__links-group' key={this.props.file.name} style={{ marginTop: isError ? '30px' : '15px' }}>
          <div className='form__links--fields form__links--fields--cv'>
            <Field
              component={Input}
              label='Document Title'
              name='documentTitle'
              className={inputClassName}
              placeholder={this.props.t('profile.additional.sections.files.placeholder')}
              disableGroup
              disabled={!editMode}
              onChange={this.onFileNameChange}
            />
          </div>
          <div className='file-input__button-group'>
            {
              editMode ? (
                <BigJustIconButton className='file-input__button--controls' type='button' onClick={this.updateFile}><i className='ion-md-checkmark' /></BigJustIconButton>
              ) : (
                <BigJustIconButton className='file-input__button--controls' type='button' onClick={this.turnEditModeOn}><i className='icon-im-edit' /></BigJustIconButton>
              )
            }
            <BigJustIconButton className='file-input__button--controls' type='button' onClick={this.props.onDelete}><i className='ion-md-trash' /></BigJustIconButton>
            {
              this.props.file.isDefaultCv
                ? <PrimaryButton className='file-input__button--controls file-input__button--cv file-input__button--cv--disabled' type='button' withIcon iconClassName='ion-md-checkmark' disabled>{t('common.default')}</PrimaryButton>
                : <Tooltip position='top' content={t('profile.additional.sections.CV.defaultCvTooltip')}>
                  <Button className='file-input__button--controls file-input__button--cv' type='button' onClick={this.props.onSetCvAsDefault}>{t('common.markAsDefault')}</Button>
                </Tooltip>
            }
          </div>
        </div>
      </Form>
    )
  }
}

UploadedFileForm.defaultProps = {
  defaultEditMode: false,
  afterSubmit: () => {}
}

export default withTranslations(UploadedFileForm)



