import React from 'react'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import TimeSlotsSelectionWeb from './TimeSlotsSelection'
import TimeSlotsSelectionMobile from './TimeSlotsSelection.mobile'

const TimeSlotsSelection = props => props.isMobile ? <TimeSlotsSelectionMobile {...props} /> : <TimeSlotsSelectionWeb {...props} />

export default withDeviceWidthDetector(TimeSlotsSelection)



