import React, { PureComponent } from 'react'
import withTranslations from 'Shared/hoc/withTranslations/index'
import PropTypes from 'prop-types'
import TimeSlot from './components/TimeSlot'
import { REQUEST_CHANGE_TIME_SLOT } from 'Shared/constants/JobOfferApplicationTimeSlots'
import Modal from 'Shared/components/Modal'
import RequestTimeSlotChangeFormConnector from 'Chat/connectors/Forms/RequestTimeSlotChange/RequestTimeSlotChangeFormConnector'
import { capitalizeWords } from 'Shared/helpers/string'
import cx from 'classnames'

import './TimeSlotsSelection.scss'
import moment from 'moment/moment'

class TimeSlotsSelection extends PureComponent {
  static propTypes = {
    timeSlots: PropTypes.array.isRequired,
    isAnyTimeSlotChosen: PropTypes.bool.isRequired,
    setTimeSlotAsChosen: PropTypes.func.isRequired,
    submitRequestChangeTimeSlots: PropTypes.func.isRequired,
    hasRequestChangeTimeSlotsSubmitSucceeded: PropTypes.bool.isRequired,
    scrollToBottom: PropTypes.func
  }

  static defaultProps = {
    scrollToBottom: () => {}
  }

  state = {
    isLoading: false,
    selectedSlotId: null,
    isRequestChangeModalVisible: false
  }

  componentDidMount () {
    this.props.scrollToBottom()
  }

  componentDidUpdate (prevProps) {
    if (this.props.hasRequestChangeTimeSlotsSubmitSucceeded &&
    prevProps.hasRequestChangeTimeSlotsSubmitSucceeded !== this.props.hasRequestChangeTimeSlotsSubmitSucceeded) {
      this.closeRequestChangeModal()
    }
  }

  setSelectedSlot = status => () => this.setState({ selectedSlotId: status })

  openRequestChangeModal = () => this.setState({ isRequestChangeModalVisible: true })
  closeRequestChangeModal = () => this.setState({ isRequestChangeModalVisible: false })

  onRequestChangeConfirm = async () => {
    this.setState({ isLoading: true })
    await this.props.submitRequestChangeTimeSlots()
    return this.setState({ isLoading: false, selectedSlotId: null })
  }
  onConfirmTimeSlot = timeSlot => async () => {
    const { t, jobOfferTitle, userName } = this.props
    this.setState({ isLoading: true })
    await this.props.setTimeSlotAsChosen(timeSlot.id, t('timeSlots.acceptWithTimeSlotDefaultMessage', {
      firstName: capitalizeWords(userName),
      jobOfferTitle,
      timeSlot: `${moment(timeSlot.dateFrom).utc().format('ll')}, ${moment(timeSlot.dateFrom).utc().format('hh:mm:A')} - ${moment(timeSlot.dateTo).utc().format('hh:mm:A')}`
    }))
    return this.setState({ isLoading: false, selectedSlotId: null })
  }

  getTimeSlots = () => {
    const { isAnyTimeSlotChosen, chosenTimeSlotId } = this.props

    return this.props.timeSlots.map(slot =>
      <TimeSlot
        key={slot.id}
        timeSlot={slot}
        isSelected={slot.id === this.state.selectedSlotId}
        isLoading={this.state.isLoading}
        isChosenTimeSlot={chosenTimeSlotId === slot.id}
        isAnyTimeSlotChosen={isAnyTimeSlotChosen}
        onTimeSlotClick={this.setSelectedSlot(slot.id)}
        onConfirm={this.onConfirmTimeSlot(slot)}
      />)
  }

  render () {
    const { t, chosenTimeSlotId, isAnyTimeSlotChosen, applicationId, channelSid, isRequestChangeTimeSlotsFormSubmitting } = this.props
    const { isLoading, selectedSlotId } = this.state
    const isApplicationsView = this.props.match.path === '/candidate/dashboard'
    const descriptionClass = cx({
      'time-slot-selection__description': true,
      'time-slot-selection__description--small': isApplicationsView
    })

    return (
      <div className='time-slots__container'>
        { isApplicationsView && <div className='time-slot-selection__title'>Select a time slot</div> }
        <div className={descriptionClass}>{t('timeSlots.chooseTimeSlot')}</div>
        {this.getTimeSlots()}
        <TimeSlot
          isSelected={REQUEST_CHANGE_TIME_SLOT === selectedSlotId}
          isLoading={isLoading}
          isChosenTimeSlot={chosenTimeSlotId === REQUEST_CHANGE_TIME_SLOT}
          isAnyTimeSlotChosen={isAnyTimeSlotChosen}
          onTimeSlotClick={this.setSelectedSlot(REQUEST_CHANGE_TIME_SLOT)}
          onConfirm={this.openRequestChangeModal}
          isChangeRequest
        />
        <Modal
          title={t('modals.requestTimeSlotChange.title')}
          handleSubmit={this.onRequestChangeConfirm}
          handleClose={this.closeRequestChangeModal}
          show={this.state.isRequestChangeModalVisible}
          handleCancel={this.closeRequestChangeModal}
          disableSubmitButton={isRequestChangeTimeSlotsFormSubmitting}
          cancelText={t('common.cancel')}
          submitText={isRequestChangeTimeSlotsFormSubmitting ? `${t('common.loading')}...` : t('common.confirm')}
          showContact={false}
          showCancel
          closeOnSubmit={false}
        >
          <p>{t('modals.requestTimeSlotChange.description')}</p>
          <RequestTimeSlotChangeFormConnector channelSid={channelSid} applicationId={applicationId} />
        </Modal>
      </div>
    )
  }
}

export default withTranslations(TimeSlotsSelection)



