import { t } from 'i18next'

export const MIN_SKILLS_COUNT = 3

export const experienceTypes = {
  employment: 'employment',
  volunteering: 'volunteering',
  studentOrganisation: 'student-organisation'
}

export const activityOptions = () => [
  {value: experienceTypes.volunteering, label: t('profile.experience.sections.volunteering.options.volunteering')},
  {value: experienceTypes.studentOrganisation, label: t('profile.experience.sections.volunteering.options.studentOrganisations')}
]



