import React, { Component } from 'react'
import { Form, Field } from 'redux-form'
import withTranslations from 'Shared/hoc/withTranslations'
import { Select, Input } from 'Shared/components/FormElements'
import { UPLOAD_DOCUMENT_TYPES_SELECT } from 'Shared/constants/Files'
import { BigJustIconButton } from 'Shared/components/Button'
import { isFileTitleTaken } from 'Shared/helpers/files'
import NotificationService from 'Shared/services/NotificationService'
import cx from 'classnames'

const R = require('ramda')

class UploadedFileForm extends Component {
  state = {
    hasNameError: false,
    fileName: ''
  }

  updateFile = async () => {
    if (isFileTitleTaken(this.props.files, { name: this.state.fileName })) {
      this.setState({ hasNameError: true })
      return NotificationService.info(this.props.t('alerts.fileExists'))
    }

    await this.props.submit()
    return this.props.afterSubmit()
  }

  onFileNameChange = event => {
    this.setState({ hasNameError: false, fileName: event.target.value })
  }

  render () {
    // Hack for no parent selector in css (tracking field meta and adjusting margin, on lower resolutions it is done by css)
    const isError = !!(window.innerWidth > 767 && this.props.formErrors.documentTitle && R.path(['documentTitle', 'touched'], this.props.formMeta))
    const inputClassName = cx({
      'form__links-group--input hidden__label': true,
      'form__links-group--input-error': this.state.hasNameError
    })

    return (
      <Form onSubmit={this.props.handleSubmit}>
        <div className='form__links-group' key={this.props.file.name} style={{ marginTop: isError ? '30px' : '15px' }}>
          <div className='form__links--fields'>
            <Field
              component={Select}
              searchable={false}
              options={UPLOAD_DOCUMENT_TYPES_SELECT()}
              placeholder={this.props.t('profile.additional.sections.files.selectType')}
              name='documentType'
              withIcon
            />
            <Field
              component={Input}
              onChange={this.onFileNameChange}
              label='Document Title'
              name='documentTitle'
              className={inputClassName}
              placeholder={this.props.t('profile.additional.sections.files.placeholder')}
              disableGroup
            />
          </div>
          <div className='file-input__button-group'>
            <BigJustIconButton className='file-input__button--controls' justIcon type='button' disabled={this.props.pristine && !this.props.defaultEditMode} onClick={this.updateFile}><i className='ion-md-checkmark' /></BigJustIconButton>
            <BigJustIconButton className='file-input__button--controls' justIcon type='button' onClick={this.props.onDelete}><i className='ion-md-trash' /></BigJustIconButton>
          </div>
        </div>
      </Form>
    )
  }
}

UploadedFileForm.defaultProps = {
  afterSubmit: () => {},
  defaultEditMode: false
}

export default withTranslations(UploadedFileForm)



