import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Form, Field } from 'redux-form'
import { normalizeRadio } from 'Shared/helpers/forms'
import { RadioWrapper, Radio } from 'Shared/components/FormElements'
import withTranslations from 'Shared/hoc/withTranslations'

class WorkExperience extends Component {
  static propTypes = {
    onFormSubmit: PropTypes.func.isRequired
  }

  render () {
    const {
      t,
      handleSubmit,
      onFormSubmit
    } = this.props
    return (
      <Form onSubmit={handleSubmit(onFormSubmit)}>
        <div className='form__group'>
          <RadioWrapper legend={t('candidate.completeProfile.experience.workExperienceLabel')}>
            <Field
              className='step_basic__radio_button'
              component={Radio}
              text={t('common.yesIDo')}
              normalize={normalizeRadio}
              name='hasWorkExperience'
              radioButtonValue
            />
            <Field
              className='step_basic__radio_button'
              component={Radio}
              text={t('common.noIDont')}
              name='hasWorkExperience'
              normalize={normalizeRadio}
              radioButtonValue={false}
            />
          </RadioWrapper>
        </div>
      </Form>
    )
  }
}

export default withTranslations(WorkExperience)



