import React from 'react'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import InterviewRequestWeb from './InterviewRequest'
import InterviewRequestMobile from './InterviewRequest.mobile'

const InterviewRequest = props => props.isMobile ? <InterviewRequestMobile {...props} /> : <InterviewRequestWeb {...props} />

export default withDeviceWidthDetector(InterviewRequest)



