import { connect } from 'react-redux'
import { reduxForm } from 'redux-form'
import { withRouter } from 'react-router-dom'
import LanguageDetails from 'Candidate/forms/Profile/LanguageDetails'
import { editCandidateLanguage } from 'Shared/ducks/languages'
import validate from './LanguageFormValidator'
import FormUtilsService from 'Shared/services/FormUtilsService'
import { mergeProps, transformToArray } from 'Shared/helpers/redux'
import { fetchLanguagesIfNeeded } from 'Shared/ducks/resources'
import { scrollToFirstError } from 'Shared/helpers/scroll'

const formUtils = new FormUtilsService()

export const formName = 'CompleteProfile-LanguageForm'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmitFail: scrollToFirstError,
  enableReinitialize: true
})(LanguageDetails)

const mapStateToProps = (state, props) => {
  const dynamicFormName = `${formName}-${props.details.id}`

  return ({
    form: dynamicFormName,
    initialValues: {
      candidateId: state.user.details.id,
      id: props.details.id,
      languageCode: props.details.languageCode.toUpperCase(),
      speaking: props.details.speaking,
      writing: props.details.writing,
      reading: props.details.reading
    },
    languageOptions: formUtils.mapCollectionToOptions(transformToArray(state.resources.languages.entries), 'code', 'language')
  })
}

const mapDispatchToProps = dispatch => ({
  onFormSubmit: values => dispatch(editCandidateLanguage(values)),
  fetchLanguages: () => dispatch(fetchLanguagesIfNeeded())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps, mergeProps)(Form))



