import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Field, Form } from 'redux-form'
import withTranslations from 'Shared/hoc/withTranslations'
import { Input, Select } from 'Shared/components/FormElements'
import Button, { PrimaryButtonOutline } from 'Shared/components/Button'
import UniversityAlgoliaField from 'Candidate/components/UniversityAlgoliaField'
import './EducationDetails.scss'
import NotificationService from 'Shared/services/NotificationService'
import { t } from 'i18next'

class EducationDetails extends Component {
  static propTypes = {
    closeOnSubmit: PropTypes.func.isRequired,
    graduationYears: PropTypes.array.isRequired,
    handleDelete: PropTypes.func.isRequired
  }

  async componentWillMount () {
    await this.props.fetchMajors()
    this.props.fetchEducationResources()
  }

  _handleGradeSystemChange = () => {
    this.props.resetGradeFormat()
    this.props.resetGradeValue()
  }

  _handleGradeFormatChange = () => {
    this.props.resetGradeValue()
  }

  _onFormSubmit = values => {
    this.props.onFormSubmit(values)
      .then(this.props.closeOnSubmit)
  }

  handleDelete = () => this.props.disableDelete ? NotificationService.error(t('alerts.universityDeleteDisabled')) : this.props.handleDelete()

  render () {
    const { t, handleSubmit, handleDelete } = this.props

    return (
      <Form onSubmit={handleSubmit(this._onFormSubmit)}>
        <div className='step_education_body__form'>
          <UniversityAlgoliaField
            onSelect={this.props.chooseUniversity}
            defaultRefinement={this.props.universityName}
          />
          <Field
            component={Select}
            options={this.props.degrees}
            optional={t('common.optional')}
            label={t('forms.degree.label')}
            placeholder={t('forms.degree.placeholder')}
            name='degree'
            withIcon
          />
          <Field
            component={Select}
            options={this.props.majors}
            label={t('forms.major.label')}
            placeholder={t('forms.major.placeholder')}
            name='majorId'
          />
          <Field
            component={Input}
            label={t('forms.specialisation.label')}
            placeholder={t('forms.specialisation.placeholder')}
            name='specialisation'
            optional={t('common.optional')}
            type='text'
          />
          <div className='columns form__wrapper--inline'>
            <div className='column col-8 col-xs-12'>
              <Field
                component={Select}
                options={this.props.graduationMonths}
                label={t('forms.graduationMonth.label')}
                placeholder={t('forms.graduationMonth.placeholder')}
                name='graduationMonth'
                withIcon
              />
            </div>
            <div className='column col-4 col-xs-12'>
              <Field
                component={Select}
                options={this.props.graduationYears}
                label={t('forms.graduationYear.label')}
                placeholder={t('forms.graduationYear.placeholder')}
                name='graduationYear'
                withIcon
              />
            </div>
          </div>
          <Field
            component={Select}
            options={this.props.gradeSystemOptions}
            onChangeCallback={this._handleGradeSystemChange}
            label={t('forms.gradingSystem.label')}
            placeholder={t('forms.gradingSystem.placeholder')}
            name='gradeSystem'
            withIcon
          />
          <div className='columns form__wrapper--inline'>
            <div className='column col-6 col-xs-12'>
              <Field
                component={Select}
                options={this.props.gradeFormatOptions}
                onChangeCallback={this._handleGradeFormatChange}
                label={t('forms.gradeFormat.label')}
                placeholder={t('forms.gradeFormat.placeholder')}
                name='gradeFormat'
                withIcon
              />
            </div>
            <div className='column col-6 col-xs-12'>
              <Field
                component={Select}
                options={this.props.gradeValueOptions}
                label={t('forms.gradeValue.label')}
                placeholder={t('forms.gradeValue.placeholder')}
                name='gradeValue'
                withIcon
              />
            </div>
          </div>
        </div>
        <div className='form__wrapper--button-group'>
          <PrimaryButtonOutline type='submit'>{t('common.save')}</PrimaryButtonOutline>
          { handleDelete && <Button onClick={this.handleDelete} justIcon><i className='ion-md-trash' /></Button> }
        </div>
      </Form>
    )
  }
}

EducationDetails.defaultProps = {
  closeOnSubmit: () => {}
}

export default withTranslations(EducationDetails)



