import { connect } from 'react-redux'
import { reduxForm, change } from 'redux-form'
import { withRouter } from 'react-router-dom'
import EducationDetailsForm from 'Candidate/forms/Profile/EducationDetailsForm'
import FormUtilsService from 'Shared/services/FormUtilsService'
import { transformToArray } from 'Shared/helpers/redux'
import { getGradeFormat, getGradeValue } from 'Shared/selectors/education'
// FIXME use fetchIfNeeded...
import { months } from 'Shared/constants/Date'
import {
  fetchEducationResources,
  fetchCountriesIfNeeded
} from 'Shared/ducks/resources'
import { fetchMajors } from 'Shared/ducks/majors'
import { addEducationDetails } from 'Shared/ducks/education'
import validate from './validate'
import INDEX_NAME from 'Shared/constants/Algolia'
import withAlgolia from 'Algolia/hoc/withAlgolia'
import { scrollToFirstError } from 'Shared/helpers/scroll'
import { getEducationYearsAsOptions } from 'Shared/helpers/date'

const formUtilsService = new FormUtilsService()
export const formName = 'CompleteProfile-AddEducationDetails'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmitFail: scrollToFirstError,
  enableReinitialize: true
})(EducationDetailsForm)

const mapStateToProps = (state) => {
  const { resources } = state

  return ({
    indexName: INDEX_NAME.UNIVERSITIES,
    shouldUpdateUrl: false,
    countries: formUtilsService.mapCollectionToOptions(transformToArray(resources.countries.entries), 'code', 'nationality'),
    universities: formUtilsService.mapCollectionToOptions(transformToArray(state.universities.entries), 'id', 'name'),
    degrees: formUtilsService.mapCollectionToOptions(state.resources.degrees.entries, 'degree_code', 'name'),
    majors: formUtilsService.mapOptionsByLanguageTranslations(transformToArray(state.majors.entries)),
    graduationYears: getEducationYearsAsOptions(),
    graduationMonths: formUtilsService.mapArrayToOptions(months(), false, true),
    gradeSystemOptions: formUtilsService.mapCollectionToOptions(
      transformToArray(resources.education.gradingSystem.entries), 'system_code', 'name'),
    gradeValueOptions: formUtilsService.mapCollectionToOptions(
      transformToArray(getGradeValue(formName)(state)), 'value_code', 'name'),
    gradeFormatOptions: formUtilsService.mapCollectionToOptions(
      transformToArray(getGradeFormat(formName)(state)), 'format_code', 'name')
  })
}

const mapDispatchToProps = (dispatch) => ({
  fetchCountries: () => dispatch(fetchCountriesIfNeeded()),
  fetchEducationResources: () => dispatch(fetchEducationResources()),
  fetchMajors: () => dispatch(fetchMajors()),
  resetGradeFormat: () => dispatch(change(formName, 'gradeFormat', null)),
  resetGradeValue: () => dispatch(change(formName, 'gradeValue', null)),
  chooseUniversity: value => dispatch(change(formName, 'universityId', value)),
  onFormSubmit: values => dispatch(addEducationDetails(values))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(withAlgolia(Form)))



