import React, { PureComponent } from 'react'
import cx from 'classnames'
import withTranslations from 'Shared/hoc/withTranslations/index'
import PropTypes from 'prop-types'
import Button, { PrimaryButton } from 'Shared/components/Button/index'
import { InterviewInvitationStatuses as invitationStatuses } from 'Shared/constants/Interviews'
import TimeSlotsSelectionConnector from 'Chat/connectors/Components/TimeSlotsSelection/TimeSlotsSelectionConnector'

import './InterviewRequest.scss'

class InterviewRequest extends PureComponent {
  static propTypes = {
    receiverName: PropTypes.string.isRequired,
    jobOfferTitle: PropTypes.string.isRequired,
    applicationId: PropTypes.string,
    onAcceptInterviewRequest: PropTypes.func.isRequired,
    onRejectInterviewRequest: PropTypes.func.isRequired,
    interviewInvitationStatus: PropTypes.string.isRequired,
    sendMessage: PropTypes.func.isRequired,
    isAnyTimeSlotChosen: PropTypes.bool.isRequired,
    chosenTimeSlot: PropTypes.object,
    scrollToBottom: PropTypes.func
  }

  state = {
    isLoading: false,
    selectedStatus: null,
    showTimeSlots: false
  }

  setSelectedStatus = status => () => this.setState({ selectedStatus: status })

  onConfirm = async () => {
    const {
      t,
      onAcceptInterviewRequest,
      onRejectInterviewRequest,
      jobOfferTitle,
      receiverName,
      applicationId,
      sendMessage,
      hasApplicationAnyTimeSlots
    } = this.props

    this.setState({ isLoading: true })

    switch (true) {
      case this.state.selectedStatus === invitationStatuses.accepted && !hasApplicationAnyTimeSlots:
        onAcceptInterviewRequest(applicationId)
        await sendMessage(t('chat.interviewRequestResponse.accept', { positionTitle: jobOfferTitle, companyName: receiverName }))
        return this.setState({ isLoading: false, selectedStatus: null })
      case this.state.selectedStatus === invitationStatuses.rejected:
        onRejectInterviewRequest(applicationId)
        await sendMessage(t('chat.interviewRequestResponse.reject', { positionTitle: jobOfferTitle, companyName: receiverName }))
        return this.setState({ isLoading: false, selectedStatus: null })
      default:
        return false
    }
  }

  render () {
    const { t, interviewInvitationStatus, hasApplicationAnyTimeSlots, applicationId, jobOfferTitle, channelSid, scrollToBottom } = this.props
    const { isLoading } = this.state
    const shouldShowConfirmButton = this.state.selectedStatus === invitationStatuses.rejected || (this.state.selectedStatus === invitationStatuses.accepted && !hasApplicationAnyTimeSlots)
    const shouldShowTimeSlots = this.state.selectedStatus === invitationStatuses.accepted && hasApplicationAnyTimeSlots

    const buttonClass = status => cx({
      'interview-invitation__button': true,
      'button--active': status === this.state.selectedStatus || status === interviewInvitationStatus,
      'interview-invitation__button--selected': shouldShowConfirmButton
    })

    return (
      <div className='interview-invitation'>
        <Button
          withIcon
          iconClassName='icon-im-accept-outline'
          className={buttonClass(invitationStatuses.accepted)}
          disabled={interviewInvitationStatus === invitationStatuses.rejected || isLoading}
          onClick={this.setSelectedStatus(invitationStatuses.accepted)}>{t('common.accept')}</Button>
        <Button
          withIcon
          iconClassName='icon-im-decline-outline'
          className={buttonClass(invitationStatuses.rejected)}
          disabled={interviewInvitationStatus === invitationStatuses.accepted || isLoading}
          onClick={this.setSelectedStatus(invitationStatuses.rejected)}>{t('common.reject')}</Button>
        {
          shouldShowConfirmButton &&
          <PrimaryButton
            className='interview-invitation__button--confirm'
            onClick={this.onConfirm}
            disabled={isLoading}
          >
            {isLoading ? `${t('common.loading')}...` : t('common.confirm')}
          </PrimaryButton>
        }
        { shouldShowTimeSlots && <TimeSlotsSelectionConnector scrollToBottom={scrollToBottom} channelSid={channelSid} jobOfferTitle={jobOfferTitle} applicationId={applicationId} /> }
      </div>
    )
  }
}

export default withTranslations(InterviewRequest)



