import React from 'react'
import Icon from 'Shared/components/Icon/index'
import Wrapper from './components/Wrapper'
import './DashboardCard.scss'

const DashboardCard = props => (
  <div className='c-dashboard-card'>
    <div className='c-dashboard-card__container'>
      {props.children}
    </div>
  </div>
)

const DashboardIcon = props => (
  <Icon className='c-dashboard-card__icon' style={props.style} {...props} />
)

DashboardCard.Icon = DashboardIcon
DashboardCard.Wrapper = Wrapper

export default DashboardCard



