import { connect } from 'react-redux'
import { reduxForm, getFormSyncErrors, getFormMeta } from 'redux-form'
import { updateCandidateFileById, setCandidateDefaultCv } from 'Shared/ducks/files'
import UploadedCvFileForm from 'Candidate/forms/Profile/UploadedCvFileForm'
import { getAllCandidateFiles } from 'Shared/selectors/files'
import validate from './UploadedFileFormValidator'

export const formName = 'EditUploadedCvFileForm'

let Form = reduxForm({
  form: formName,
  validate,
  enableReinitialize: true,
  onSubmit: async (values, dispatch, props) => dispatch(updateCandidateFileById(props.file.id, values))
})(UploadedCvFileForm)

const mapStateToProps = (state, ownProps) => {
  const dynamicFormName = `${formName}-${ownProps.file.id}`

  return ({
    initialValues: {
      documentType: ownProps.file.documentType,
      documentTitle: ownProps.file.title
    },
    form: dynamicFormName,
    formErrors: getFormSyncErrors(dynamicFormName)(state),
    formMeta: getFormMeta(dynamicFormName)(state),
    files: getAllCandidateFiles()(state)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  onSetCvAsDefault: () => dispatch(setCandidateDefaultCv(ownProps.file.id))
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)



