export default {
  SKILLS: 'SkillTranslations',
  UNIVERSITIES: 'Universities',
  HIGH_SCHOOLS: 'HighSchools',
  JOB_OFFERS: 'JobOffers',
  JOB_OFFERS_RECENT: 'jobOffersByPublishedAtDesc',
  CANDIDATES: 'Candidates', // CandidatesByATSScoreDesc
  CANDIDATES_LAST_LOGGED_IN: 'CandidatesByLoggedInAtDesc'
}

export const INFINITE_SEARCH_ITEMS_PER_PAGE_COUNT = 9
export const SEARCH_ITEMS_PER_PAGE_MOBILE_COUNT = 10
export const APPLICANTS_HITS_PER_PAGE_LIMIT = 9
export const APPLICANTS_ALL_HITS_PER_PAGE_LIMIT = 300

export const MIN_AGE_FILTER = 15
export const MAX_AGE_FILTER = 28
export const UNKNOWN_UNIVERSITY = '(unknown)'

export const AGE_FILTER_NAME = 'Age'
export const UNIVERSITY_FILTER_NAME = 'University'

export const LAST_ACTIVITY_RADIO_FILTER_ITEMS = [
  {inputValue: 'all', translationPath: 'searchAttributes.loggedInAt.allTime'},
  {inputValue: 'seven', translationPath: 'searchAttributes.loggedInAt.sevenDays'},
  {inputValue: 'thirty', translationPath: 'searchAttributes.loggedInAt.thirtyDays'},
  {inputValue: 'ninety', translationPath: 'searchAttributes.loggedInAt.ninetyDays'},
  {inputValue: 'year', translationPath: 'searchAttributes.loggedInAt.lastYear'}
]

export const LAST_ACTIVITY_INPUT_VALUES = LAST_ACTIVITY_RADIO_FILTER_ITEMS.reduce((prev, next) => {
  prev[next.inputValue] = { value: next.inputValue, translationPath: next.translationPath }
  return prev
}, {})

export const DATE_POSTED_RADIO_FILTER_ITEMS = [
  {inputValue: 'all', translationPath: 'searchAttributes.datePosted.all'},
  {inputValue: 'day', translationPath: 'searchAttributes.datePosted.day'},
  {inputValue: 'threeDays', translationPath: 'searchAttributes.datePosted.threeDays'},
  {inputValue: 'week', translationPath: 'searchAttributes.datePosted.week'},
  {inputValue: 'month', translationPath: 'searchAttributes.datePosted.month'}
]

export const DATE_POSTED_INPUT_VALUES = DATE_POSTED_RADIO_FILTER_ITEMS.reduce((prev, next) => {
  prev[next.inputValue] = { value: next.inputValue, translationPath: next.translationPath }
  return prev
}, {})



