import { reduxForm, formValueSelector, change } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import FormUtilsService from 'Shared/services/FormUtilsService'
import ExperienceDetails from 'Candidate/forms/Profile/ExperienceDetails'
import { transformToArray } from 'Shared/helpers/redux'
import { addExperience } from 'Shared/ducks/experiences'
import { experienceTypes } from 'Shared/constants/Experience'
import validate from './ExperienceFormValidator'
import { scrollToFirstError } from 'Shared/helpers/scroll'

const formUtilsService = new FormUtilsService()

export const formName = 'CompleteProfile-AddWorkExperience'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmitFail: scrollToFirstError,
  destroyOnUnmount: true,
  enableReinitialize: true
})(ExperienceDetails)

const mapStateToProps = state => ({
  organisations: formUtilsService.mapCollectionToOptions(transformToArray(state.organisations.entries), 'id', 'name'),
  currentWork: formValueSelector(formName)(state, 'currentWork'),
  initialValues: {
    type: experienceTypes.employment,
    candidateId: state.user.details.id,
    currentWork: false
  }
})

const mapDispatchToProps = dispatch => ({
  onFormSubmit: values => dispatch(addExperience(values)),
  onCurrentWorkChange: () => dispatch(change(formName, 'finishedAt', null))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form))



