import { prop, head, innerJoin, pipe } from 'ramda'
import { animateScroll } from 'react-scroll'

export const scrollToFirstError = errors => {
  const compensatedOffset = 200
  const errorKeys = Object.keys(errors)
  const formAnchors = Array.from(document.querySelectorAll('[name^=error-position-]'))
    .map(el => {
      return {
        offsetTop: el.getBoundingClientRect().top + window.scrollY,
        name: el.attributes.name.value
      }
    })
    .slice()
    .sort((a, b) => a.offsetTop - b.offsetTop)

  const positionsWithErrors = innerJoin(
    (errorEl, errorKey) => errorEl.name.indexOf(errorKey) >= 0,
    formAnchors,
    errorKeys
  )

  const smallestOffsetTop = pipe(
    head,
    prop('offsetTop')
  )(positionsWithErrors)

  const offsetToScrollTo = smallestOffsetTop - compensatedOffset

  return smallestOffsetTop ? animateScroll.scrollTo(offsetToScrollTo, { smooth: true }) : null
}



