import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Field, Form, submit } from 'redux-form'
import {
  Select,
  RatingStars,
  Input
} from 'Shared/components/FormElements'
import Button, { PrimaryButtonOutline } from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations'
import { degree } from 'Shared/constants/Languages'

class LanguageDetails extends Component {
  static propTypes = {
    closeOnSubmit: PropTypes.func.isRequired,
    onFormSubmit: PropTypes.func.isRequired,
    handleDelete: PropTypes.func.isRequired,
    languageOptions: PropTypes.array.isRequired
  }

  dispatchSubmit = () => this.props.dispatch(submit(this.props.form))

  _onFormSubmit = values => {
    this.props.onFormSubmit(values)
      .then(() => this.props.closeOnSubmit())
  }

  render () {
    const {
      t,
      handleSubmit,
      languageOptions,
      handleDelete
    } = this.props
    return (
      <Form onSubmit={handleSubmit(this._onFormSubmit)}>
        <div className='form__group'>
          <Field
            component={Select}
            label={t('forms.language.label')}
            options={languageOptions}
            placeholder={t('forms.language.placeholder')}
            name='languageCode'
            withIcon
          />
          <div className='form__group'>
            <div className='columns'>
              <div className='column col-4 col-xs-12'>
                <Field
                  component={RatingStars}
                  label='spoken'
                  name='speaking'
                  starCount={3}
                  quiet
                  degreeDescription={degree()}
                  withContainer
                />
              </div>
              <div className='column col-4 col-xs-12'>
                <Field
                  component={RatingStars}
                  label='written'
                  name='writing'
                  starCount={3}
                  quiet
                  degreeDescription={degree()}
                  withContainer
                />
              </div>
              <div className='column col-4 col-xs-12'>
                <Field
                  component={RatingStars}
                  label='read'
                  name='reading'
                  starCount={3}
                  quiet
                  degreeDescription={degree()}
                  withContainer
                />
              </div>
            </div>
          </div>
          <Field
            component={Input}
            name='candidateId'
            type='hidden'
          />
          <Field
            component={Input}
            name='id'
            type='hidden'
          />
          <div className='form__wrapper--button-group'>
            <PrimaryButtonOutline onClick={this.dispatchSubmit}>{t('common.save')}</PrimaryButtonOutline>
            <Button onClick={handleDelete} justIcon><i className='ion-md-trash' /></Button>
          </div>
        </div>
      </Form>
    )
  }
}

export default withTranslations(LanguageDetails)



