import { connect } from 'react-redux'
import Availability from 'Shared/components/Availability/Availability.mobile'
import { isStillAvailable } from 'Shared/helpers/date'

const mapStateToProps = state => {
  const { availableFrom, availableTo } = state.user.details

  return ({
    availableFrom,
    availableTo,
    isCandidateAvailable: isStillAvailable(availableTo)
  })
}

export default connect(mapStateToProps)(Availability)



