import { connect } from 'react-redux'
import { reduxForm } from 'redux-form'
import moment from 'moment'
import CertificateDetailsForm from 'Candidate/forms/Profile/CertificateDetailsForm'
import validate from './validateCertificates'
import { generatePastYearsList } from 'Shared/helpers/date'
import FormUtilsService from 'Shared/services/FormUtilsService'
import { editCertificate } from 'Shared/ducks/certificates'

export const formName = 'CompleteProfile-CertificatesDetails'

const formUtilsService = new FormUtilsService()

let Form = reduxForm({
  form: formName,
  validate,
  enableReinitialize: true
})(CertificateDetailsForm)

const mapStateToProps = (state, props) => {
  const dynamicFormName = `${formName}-${props.details.id}`

  return ({
    form: dynamicFormName,
    years: formUtilsService.mapArrayToOptions(generatePastYearsList()),
    initialValues: {
      name: props.details.name,
      issuer: props.details.issuer,
      issuedAt: moment(props.details.issuedAt).year(),
      certificateId: props.details.id
    }
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  saveCertificate: values => dispatch(editCertificate(values))
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)



