import { connect } from 'react-redux'
import InterviewRequest from 'Chat/components/InterviewRequest'
import { withRouter } from 'react-router-dom'
import { sendMessage } from 'Chat/ducks/classes/messages'
import { Roles } from 'Shared/constants/User'
import { getCandidateFullName } from 'Shared/selectors/candidates'
import { getOrganisationName } from 'Shared/selectors/organisations'
import { respondInterviewInvitation } from 'Shared/ducks/jobOfferApplications'
import { getInterviewInvitationStatus } from 'Shared/selectors/jobOfferApplications'
import { InterviewInvitationStatuses } from 'Shared/constants/Interviews'
import { isAnyTimeSlotChosen, chosenTimeSlot, hasApplicationAnyTimeSlots } from 'Shared/selectors/jobOfferApplicationTimeSlots'

const mapStateToProps = (state, ownProps) => {
  const { role } = state.user
  const channelSid = ownProps.match.params.channelSid || ownProps.channelSid
  const {
    attributes: {
      candidateId,
      organisationId,
      applicationId
    },
    friendlyName
  } = state.chat.channels.entries[channelSid]

  const receiverMap = {
    name: {
      [Roles.candidate.name]: getOrganisationName(state, organisationId),
      [Roles.employer.name]: getCandidateFullName(state, candidateId)
    }
  }

  return ({
    applicationId,
    jobOfferTitle: friendlyName,
    receiverName: receiverMap.name[role],
    interviewInvitationStatus: getInterviewInvitationStatus(state, applicationId),
    hasApplicationAnyTimeSlots: hasApplicationAnyTimeSlots(state, applicationId),
    isAnyTimeSlotChosen: isAnyTimeSlotChosen(state, applicationId),
    chosenTimeSlot: chosenTimeSlot(state, applicationId)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => {
  const channelSid = ownProps.match.params.channelSid || ownProps.channelSid

  return ({
    sendMessage: message => dispatch(sendMessage(channelSid, message)),
    onAcceptInterviewRequest: applicationId => dispatch(respondInterviewInvitation(applicationId, InterviewInvitationStatuses.accepted)),
    onRejectInterviewRequest: applicationId => dispatch(respondInterviewInvitation(applicationId, InterviewInvitationStatuses.rejected))
  })
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(InterviewRequest))



