import { createSelector } from 'reselect'
import * as R from 'ramda'

const getSalaryRanges = state => state.salaryRange.entries

export const getExperienceRange = createSelector(
  getSalaryRanges,
  (state, currency) => currency,
  (salaryRanges, currency) => R.pathOr('', [currency, 'experience'])(salaryRanges)
)

export const getExpenseCoverageRange = createSelector(
  getSalaryRanges,
  (state, currency) => currency,
  (salaryRanges, currency) => R.pathOr('', [currency, 'expenseCoverage'])(salaryRanges)
)

export const getGradWage1Range = createSelector(
  getSalaryRanges,
  (state, currency) => currency,
  (salaryRanges, currency) => R.pathOr('', [currency, 'gradWage1'])(salaryRanges)
)

export const getGradWage2Range = createSelector(
  getSalaryRanges,
  (state, currency) => currency,
  (salaryRanges, currency) => R.pathOr('', [currency, 'gradWage2'])(salaryRanges)
)

export const getGradWage3Range = createSelector(
  getSalaryRanges,
  (state, currency) => currency,
  (salaryRanges, currency) => R.pathOr('', [currency, 'gradWage3'])(salaryRanges)
)



