import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'
import { UPLOAD_DOCUMENT_TYPES } from 'Shared/constants/Files'
import * as R from 'ramda'
import { hasDefaultCv, getDefaultCv } from 'Shared/helpers/files'
import { getApplicationCvId } from 'Shared/selectors/jobOfferApplications'

export const getFileById = state => state.files.candidate
export const getOrganisationFileById = state => state.files.organisation
export const getUploadedFilesProgress = state => state.files.fileUploadProgress

export const getCandidateNonCvFiles = createSelector(
  getFileById,
  R.pipe(
    transformToArray,
    R.filter(
      R.pipe(
        R.propEq('documentType', UPLOAD_DOCUMENT_TYPES.CV),
        R.not
      )
    ),
    R.sortBy(
      R.prop('createdAt')
    )
  )
)

export const getAllCandidateFiles = () =>
  createSelector(
    getFileById,
    getFileById => transformToArray(getFileById).sort((a, b) => {
      return a.documentType.localeCompare(b.documentType)
    })
  )

export const getAllOrganisationImages = createSelector(
  getOrganisationFileById,
  (state, organisationId) => organisationId,
  (files, organisationId) => R.pipe(
    transformToArray,
    R.filter(R.propEq('organisationId', organisationId))
  )(files)
)

export const getCandidateCvFiles = createSelector(
  getFileById,
  R.pipe(
    transformToArray,
    R.filter(
      R.propEq('documentType', UPLOAD_DOCUMENT_TYPES.CV)
    ),
    R.sortBy(
      R.prop('createdAt')
    )
  )
)

export const getCandidateFilesWithDefaultCv =
  createSelector(
    getFileById,
    getFileById => {
      const documents = transformToArray(getFileById).filter(file => file.documentType !== UPLOAD_DOCUMENT_TYPES.CV)
      hasDefaultCv(getFileById) && documents.unshift(getDefaultCv(getFileById))
      return documents
    }
  )

export const getCandidateDefaultCvId =
  createSelector(
    getFileById,
    getFileById => R.propOr('', 'id', getDefaultCv(getFileById))
  )

export const uploadedFilesProgress = createSelector(
  getUploadedFilesProgress,
  (state, fileType) => fileType,
  (filesProgress, fileType) => {
    return R.pipe(
      R.values,
      R.filter(
        R.propEq('fileType', fileType)
      )
    )(filesProgress)
  }
)

export const getDocumentCvsAsOptions = createSelector(
  getFileById,
  R.pipe(
    transformToArray,
    R.filter(
      R.propEq('documentType', UPLOAD_DOCUMENT_TYPES.CV)
    ),
    R.map(
      R.applySpec({
        label: R.prop('title'),
        value: R.prop('id')
      })
    )
  )
)

export const getDocumentById = createSelector(
  getFileById,
  (state, id) => id,
  (files, id) => R.propOr({}, id)(files)
)

export const getCandidateDefaultCvOrAllCvs = createSelector(
  getFileById,
  (files) => {
    const getAllCvs = R.pipe(
      transformToArray,
      R.filter(
        R.propEq('documentType', UPLOAD_DOCUMENT_TYPES.CV)
      ),
      R.sortBy(
        R.prop('createdAt')
      )
    )

    const getDefaultCv = R.pipe(
      transformToArray,
      R.filter(
        R.propEq('isDefaultCv', true)
      )
    )

    return R.ifElse(
      hasDefaultCv,
      getDefaultCv,
      getAllCvs
    )(files)
  }
)

export const getApplicationCvFile = createSelector(
  getFileById,
  getApplicationCvId,
  (files, id) => {
    const appendToArray = file => R.append(file, [])
    return R.pipe(
      R.propOr({}, id),
      R.ifElse(
        R.isEmpty,
        R.always([]),
        appendToArray
      )
    )(files)
  }
)

export const hasAnyAdditionalDocuments = createSelector(
  getFileById,
  R.pipe(
    transformToArray,
    R.filter(
      R.pipe(
        R.propEq('documentType', UPLOAD_DOCUMENT_TYPES.CV),
        R.not
      )
    ),
    R.isEmpty,
    R.not
  )
)



