import React from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import './EntitiesListTab.scss'
import Tooltip from 'Shared/components/Tooltip/Tooltip'

const EntitiesListTab = ({ onClick, text, badge, isActive, name, tooltip, tooltipText }) => {
  const containerClass = cx({
    'entities-list-tab__link': true,
    'entities-list-tab__link--active': isActive
  })

  const handleClick = (event) => onClick(event, name)

  return (
    tooltip
      ? <Tooltip content={tooltipText}>
        <span
          onClick={handleClick}
          className={containerClass}
        >
          <span>
            {text}
            {badge !== undefined && <div className='entities-list-tab__badge'>{badge}</div>}
          </span>
        </span>
      </Tooltip>
      : <span
        onClick={handleClick}
        className={containerClass}
      >
        <span>
          {text}
          {badge !== undefined && <div className='entities-list-tab__badge'>{badge}</div>}
        </span>
      </span>
  )
}

EntitiesListTab.propTypes = {
  text: PropTypes.string.isRequired,
  onClick: PropTypes.func,
  badge: PropTypes.number,
  name: PropTypes.string,
  tooltip: PropTypes.bool,
  tooltipText: (props, propName, componentName) => {
    if (props.tooltip !== undefined && !props[propName]) {
      return new Error(`When using tooltip prop you must provide ${propName} prop to ${componentName}`)
    } else if (props.tooltip && typeof props[propName] !== 'string') {
      return new Error(`Invalid prop supplied to ${componentName}. Received: prop ${propName} of type ${typeof props[propName]} and expected: string`)
    }
  }
}

export default EntitiesListTab



