import React, { Component } from 'react'
import { Form } from 'redux-form'
import SkillsWrapper from 'Shared/components/SkillsWrapper'
import { MAX_SKILLS_LENGTH } from 'Shared/constants/Validation'

class Skills extends Component {
  render () {
    const { handleSubmit, saveUserSkills } = this.props

    return (
      <Form onSubmit={handleSubmit(saveUserSkills)}>
        <SkillsWrapper
          skillsLength={MAX_SKILLS_LENGTH.CANDIDATE}
          selectedSkills={this.props.selectedSkills}
          addUserNewSkill={this.props.addUserNewSkill}
          pushSkill={this.props.pushSkill}
          {...this.props}
        />
      </Form>
    )
  }
}

export default Skills



