import { connectAutoComplete } from 'react-instantsearch-dom'
import { connect } from 'react-redux'
import MultiSelectInput from 'Shared/components/FormElements/Select/MultiSelectInput'
import FormUtilsService from 'Shared/services/FormUtilsService'

const formUtils = new FormUtilsService()

const mapStateToProps = (state, ownProps) => ({
  options: formUtils.mapCollectionToOptions(ownProps.hits, 'id', 'name')
})

export default connectAutoComplete(connect(mapStateToProps)(MultiSelectInput))



