import React from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import './Notification.scss'

const Notification = ({ warning, error, success, iconClassName, messageText, className }) => {
  const fieldClass = cx({
    'notification__container': true,
    'warning_message': warning,
    'error_message': error,
    'success_message': success,
    [className]: className
  })

  const iconClass = cx({
    'notification__icon': true,
    'warning_icon': warning,
    'error_icon': error,
    'success_icon': success,
    [iconClassName]: iconClassName
  })

  return (
    <div className={fieldClass}>
      <i className={iconClass} />
      <div className='notification__message'>{messageText}</div>
    </div>
  )
}

Notification.propTypes = {
  t: PropTypes.func,
  className: PropTypes.string,
  iconClassName: PropTypes.string,
  messageText: PropTypes.oneOfType([
    PropTypes.string.isRequired,
    PropTypes.node.isRequired
  ])
}

export default Notification



