import { connect } from 'react-redux'
import { reduxForm, change } from 'redux-form'
import { withRouter } from 'react-router-dom'
import EducationHighSchoolDetailsForm from 'Candidate/forms/Profile/EducationHighSchoolDetailsForm'
import FormUtilsService from 'Shared/services/FormUtilsService'
import { transformToArray } from 'Shared/helpers/redux'
import { months } from 'Shared/constants/Date'
import { toYearAndMonth, toTimestamp, getEducationYearsAsOptions } from 'Shared/helpers/date'
import validate from './validateHighSchool'
import INDEX_NAME from 'Shared/constants/Algolia'
import withAlgolia from 'Algolia/hoc/withAlgolia'
import { scrollToFirstError } from 'Shared/helpers/scroll'
import { fetchHighSchoolEducationGradingSystemIfNeeded } from 'Shared/ducks/resources'
import { editHighSchoolEducationDetails } from 'Shared/ducks/highSchoolLevelEducation'

const formUtilsService = new FormUtilsService()
export const formName = 'CompleteProfile-EditHighSchoolEducationDetails'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmitFail: scrollToFirstError,
  enableReinitialize: true
})(EducationHighSchoolDetailsForm)

const mapStateToProps = (state, ownProps) => {
  const dynamicFormName = `${formName}-${ownProps.details.id}`
  const [graduationYear, graduationMonth] = toYearAndMonth(ownProps.details.graduatedAt)

  return ({
    initialValues: {
      ...ownProps.details,
      graduationMonth,
      graduationYear
    },
    highSchoolName: ownProps.details.highSchool.name,
    candidateId: state.user.details.id,
    indexName: INDEX_NAME.HIGH_SCHOOLS,
    shouldUpdateUrl: false,
    form: dynamicFormName,
    gradingSystemOptions: formUtilsService.mapCollectionToOptions(
      transformToArray(state.resources.highSchoolEducation.gradingSystem.entries),
      'system_code',
      'name'
    ),
    graduationYears: getEducationYearsAsOptions(),
    graduationMonths: formUtilsService.mapArrayToOptions(months(), false, true)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => {
  const dynamicFormName = `${formName}-${ownProps.details.id}`

  return ({
    fetchHighSchoolEducationGradingSystem: () => dispatch(fetchHighSchoolEducationGradingSystemIfNeeded()),
    onFormSubmit: (candidateId, values) => dispatch(
      editHighSchoolEducationDetails(
        candidateId, { ...values, graduatedAt: toTimestamp(values.graduationYear, values.graduationMonth) })
    ),
    chooseHighSchool: value => dispatch(change(dynamicFormName, 'highSchoolId', value))
  })
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(withAlgolia(Form)))



