import { connect } from 'react-redux'
import { reduxForm, arrayPush, formValueSelector } from 'redux-form'
import validate from './SkillsFormValidator'
import SkillsForm from 'Shared/forms/SkillsForm'
import { saveUserSkills, addUserNewSkill } from 'Shared/ducks/skills'
import { getSkillsByUserLanguage } from 'Shared/selectors/skills'
import { ISOcodes } from 'Shared/constants/i18n'
import withAlgolia from 'Algolia/hoc/withAlgolia'
import INDEX_NAME from 'Shared/constants/Algolia'
import { scrollToFirstError } from 'Shared/helpers/scroll'

export const formName = 'CompleteProfile-Skills'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmitFail: scrollToFirstError,
  destroyOnUnmount: true,
  enableReinitialize: true
})(SkillsForm)

// TODO: use LanguageService.userLanguage if other languages will be available in algolia
const filters = { languageIsoCode: `languageIsoCode:${ISOcodes.en}` }

const mapStateToProps = state => ({
  initialValues: {
    skills: getSkillsByUserLanguage()(state)
  },
  selectedSkills: formValueSelector(formName)(state, 'skills'),
  filters,
  indexName: INDEX_NAME.SKILLS,
  shouldUpdateUrl: false
})

const mapDispatchToProps = dispatch => ({
  saveUserSkills: values => dispatch(saveUserSkills(values.skills)),
  addUserNewSkill: skill => dispatch(addUserNewSkill(skill)),
  pushSkill: skill => dispatch(arrayPush(formName, 'skills', skill))
})

export default connect(mapStateToProps, mapDispatchToProps)(withAlgolia(Form))



