import React, { PureComponent } from 'react'
import cx from 'classnames'
import withTranslations from 'Shared/hoc/withTranslations/index'
import PropTypes from 'prop-types'
import Button, { PrimaryButton } from 'Shared/components/Button/index'
import moment from 'moment'

import '../TimeSlotsSelection.scss'

class TimeSlot extends PureComponent {
  static propTypes = {
    timeSlot: PropTypes.object,
    isSelected: PropTypes.bool.isRequired,
    isLoading: PropTypes.bool.isRequired,
    isChosenTimeSlot: PropTypes.bool.isRequired,
    isAnyTimeSlotChosen: PropTypes.bool.isRequired,
    onTimeSlotClick: PropTypes.func.isRequired,
    onConfirm: PropTypes.func.isRequired,
    isChangeRequest: PropTypes.bool
  }

  render () {
    const { t, timeSlot, isLoading, isSelected, onTimeSlotClick, onConfirm, isAnyTimeSlotChosen, isChosenTimeSlot, isChangeRequest } = this.props

    const buttonClass = cx({
      'time-slot__button': true,
      'button--active': isSelected || isChosenTimeSlot,
      'time-slot__button--selected': isSelected
    })

    return (
      <div className='time-slot__container'>
        <Button
          className={buttonClass}
          disabled={isAnyTimeSlotChosen || isLoading}
          onClick={onTimeSlotClick}>
          {
            isChangeRequest ? <strong>{t('timeSlots.requestChange')}</strong> : <span>
              <strong>{moment(timeSlot.dateFrom).utc().format('LL')}</strong>&nbsp;
              <span className='time-slot__time'>{moment(timeSlot.dateFrom).utc().format('hh:mm:A')}&nbsp;-&nbsp;{moment(timeSlot.dateTo).utc().format('hh:mm:A')}</span>
            </span>
          }
        </Button>
        {
          isSelected &&
          <PrimaryButton
            className='time-slot__button--confirm'
            onClick={onConfirm}
            disabled={isLoading}
          >
            {isLoading ? `${t('common.loading')}...` : t('common.confirm')}
          </PrimaryButton>
        }
      </div>
    )
  }
}

export default withTranslations(TimeSlot)



