import startCase from 'lodash/startCase'
import replace from 'lodash/replace'

export default values => {
  const errors = {}

  const requiredFields = ['writing', 'speaking', 'reading']

  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = {
        key: 'errors.requiredField',
        options: {
          field: startCase(field)
        }
      }
    }
  })

  if (!values['languageCode']) {
    errors['languageCode'] = {
      key: 'errors.requiredField',
      options: {
        field: replace('languageCode', 'Code', '')
      }
    }
  }

  return errors
}



