import React from 'react'
import PropTypes from 'prop-types'
import { NavLink } from 'react-router-dom'
import { isNil, not } from 'ramda'
import cx from 'classnames'
import './EntitiesListTab.scss'

const EntitiesListTabLink = ({ to, onClick, text, badge, showBadge }) => {
  const badgeClass = cx({
    'entities-list-tab__badge': true,
    'entities-list-tab__badge--hidden': !showBadge
  })

  return (
    <NavLink
      to={to}
      onClick={onClick}
      className='entities-list-tab__link col-sm-4'
      activeClassName='entities-list-tab__link--active col-sm-4'
    >
      <span>
        {text}
        {not(isNil(badge)) && <span className={badgeClass}>{badge}</span>}
      </span>
    </NavLink>
  )
}

EntitiesListTabLink.defaultProps = ({
  showBadge: true
})

EntitiesListTabLink.propTypes = {
  to: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.object
  ]).isRequired,
  text: PropTypes.string.isRequired,
  onClick: PropTypes.func,
  badge: PropTypes.number
}

export default EntitiesListTabLink



