import qs from 'qs'

const searchStateToUrl = searchState => qs.stringify(searchState)

const urlToSearchState = (search, parsePrimitives) => {
  return qs.parse(search.slice(1), parsePrimitives && { decoder: parsePrimitiveTypes })
}

const parsePrimitiveTypes = (value) => {
  let keywords = {
    true: true,
    false: false,
    null: null,
    undefined: undefined
  }

  if (value in keywords) {
    return keywords[value]
  }

  return decodeURIComponent(value.replace(/\+/g, ' '))
}

export {
  searchStateToUrl,
  urlToSearchState
}



