import { reduxForm } from 'redux-form'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { sendRequestTimeSlotChange } from 'Shared/ducks/jobOfferApplications'
import { sendMessage } from 'Chat/ducks/classes/messages'
import { getRequestTimeSlotChangeDefaultMessage } from 'Shared/helpers/timeSlots'

import RequestTimeSlotChangeForm from 'Chat/forms/RequestTimeSlotChange/RequestTimeSlotChangeForm'
import validate from './RequestTimeSlotChangeFormValidator'

export const formName = 'RequestTimeSlotChangeForm'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmit: async (values, dispatch, ownProps) => {
    const channelSid = ownProps.match.params.channelSid || ownProps.channelSid
    await dispatch(sendRequestTimeSlotChange(ownProps.applicationId, values.message, channelSid))
    return dispatch(sendMessage(channelSid, values.message))
  }
})(RequestTimeSlotChangeForm)

const mapStateToProps = state => ({
  initialValues: {
    message: getRequestTimeSlotChangeDefaultMessage(state.user.details.firstName)
  }
})

export default withRouter(connect(mapStateToProps)(Form))



